/* 
  @author Chaeeun
  @category review create
 */

<template>
<div>
  <navbar></navbar>
  <div class="containerB" style="margin-top:50px;">
    <div class="editor c4" style="width:100%;">
      <div style="margin-bottom:40px;">
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
      </div>
      <div v-html="review.title"></div>
      <editor-menu-bar :editor="editor" v-slot="{ commands, isActive }">
        <div class="menubar">
          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.bold() }"
            @click="commands.bold"
          >
            <icon name="bold" id="bold" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.italic() }"
            @click="commands.italic"
          >
            <icon name="italic" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.strike() }"
            @click="commands.strike"
          >
            <icon name="strike" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.underline() }"
            @click="commands.underline"
          >
            <icon name="underline" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.code() }"
            @click="commands.code"
          >
            <icon name="code" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.paragraph() }"
            @click="commands.paragraph"
          >
            <icon name="paragraph" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.heading({ level: 1 }) }"
            @click="commands.heading({ level: 1 })"
          >H1</button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.heading({ level: 2 }) }"
            @click="commands.heading({ level: 2 })"
          >H2</button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.heading({ level: 3 }) }"
            @click="commands.heading({ level: 3 })"
          >H3</button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.bullet_list() }"
            @click="commands.bullet_list"
          >
            <icon name="ul" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.ordered_list() }"
            @click="commands.ordered_list"
          >
            <icon name="ol" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.blockquote() }"
            @click="commands.blockquote"
          >
            <icon name="quote" />
          </button>

          <button
            class="menubar__button"
            :class="{ 'is-active': isActive.code_block() }"
            @click="commands.code_block"
          >
            <icon name="code" />
          </button>

          <button class="menubar__button" @click="commands.horizontal_rule">
            <icon name="hr" />
          </button>

          <button class="menubar__button" @click="commands.undo">
            <icon name="undo" />
          </button>

          <button class="menubar__button" @click="commands.redo">
            <icon name="redo" />
          </button>

          <button class="menubar__button" @click="showImagePrompt(commands.image)">
            <icon name="image" />
          </button>

          <div>
            <input
              ref="image"
              class="file-upload-input"
              type="file"
              style="display:none;"
              @change="readURL"
              accept="image/*"
            />
          </div>
        </div>
      </editor-menu-bar>

      <div style="margin-top:30px;">
        <editor-content id="tiptaparea" class="editor__content" :editor="editor" />
      </div>

      <div style="margin-top:20px;">
        <router-link v-bind:to="{name:'ReviewCreate'}">
          <button style="margin-top:20px;">뒤로</button>
        </router-link>
        <button
          @click="preprocessing"
          class="w3-button w3-black w3-round-large"
          style="float:right;"
        >Complete!!</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import $ from "jquery";
import Navbar from "../../components/common/Navbarsub.vue";

import "../../assets/css/editor/editor.scss";
import "../../assets/css/editor/main.scss";
import "../../assets/css/editor/menubar.scss";
import "../../assets/css/editor/menububble.scss";
import "../../assets/css/editor/variables.scss";
import "../../assets/css/style.scss";
import Icon from "../../views/review/Icon.vue";
import ReviewApi from "../../apis/ReviewApi";

import { Editor, EditorContent, EditorMenuBar } from "tiptap";
import {
  Blockquote,
  CodeBlock,
  HardBreak,
  Heading,
  HorizontalRule,
  OrderedList,
  BulletList,
  ListItem,
  TodoItem,
  TodoList,
  Bold,
  Code,
  Italic,
  Link,
  Strike,
  Underline,
  History,
  Image,
  Placeholder 
} from "tiptap-extensions";

export default {
  components: {
    EditorContent,
    EditorMenuBar,
    Icon,
    Navbar
  },
  props: {
    review: { type: Object },
    item: { type: Object },
    params: { type: Object }
  },
  data() {
    return {
      text: "",
      comm: {},
      imageFiles: [],
      file: File,
      sortY: [],
      imageNames: [],
      editor: new Editor({
        extensions: [
          new Blockquote(),
          new BulletList(),
          new CodeBlock(),
          new HardBreak(),
          new Heading({ levels: [1, 2, 3] }),
          new HorizontalRule(),
          new ListItem(),
          new OrderedList(),
          new TodoItem(),
          new TodoList(),
          new Link(),
          new Bold(),
          new Code(),
          new Italic(),
          new Strike(),
          new Underline(),
          new History(),
          new Image(),
          //new Doc(),
          new Placeholder({
            emptyNodeClass: 'is-empty',
            emptyNodeText: '여기에 글을 적어주세요..',
            showOnlyWhenEditable: true
            }),
        ],
        //content: ``,
        
      })
    };
  },
  mounted() {
    $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
    });
  },
  methods: {
    dataURItoBlob(dataURI) {
      var binary = atob(dataURI.split(",")[1]);
      var array = [];
      for (var i = 0; i < binary.length; i++) {
        array.push(binary.charCodeAt(i));
      }
      return new Blob([new Uint8Array(array)], { type: "image/jpeg" });
    },
    preprocessing() {
      //대표글 파일
      if (typeof this.review.file != typeof "str") {
        this.imageFiles.push(this.review.file);
      }

      //본문 파일 변환
      var tmptext = document.getElementById("tiptaparea").innerHTML;
      var len = tmptext.length;
      tmptext = tmptext.slice(61, len - 6);
      len = tmptext.length;

      let start = 0;
      let last = 0;
      while (start >= 0) {
        start = tmptext.indexOf("data:image", start + 1);
        if (start < 0) break;

        last = tmptext.indexOf('"', start);
        console.log("start:" + start + " last:" + last);

        let tmp = this.dataURItoBlob(tmptext.substring(start, last));
        console.log(tmp);

        //let file = new FormData();
        //file.append("file", tmp);
        this.imageFiles.push(new File([tmp], "name"));
      }

      //날짜 만들기
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!
      var yyyy = today.getFullYear();
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      today = yyyy + "-" + mm + "-" + dd;

      //

      this.$set(this.review, "date", today);
      this.$set(this.review, "content", tmptext);

      this.filesave();
    },

    submit() {
      let data = {
        userIdFk: this.review.userId,
        bookIsbnFk: this.review.bookIsbn,
        title: this.review.title,
        content: this.review.content,
        date: this.review.date,
        rating: this.review.rating,
        isreply: this.review.isreply,
        backgroundImg: this.review.backgroundImg,
        font: this.review.font,
        fontSize: this.review.fontSize,
        fontColor: this.review.fontColor,
        titleText: this.review.titleText
      };

      ReviewApi.addReview(
        data,
        res => {
          console.log(res);
          //alert("서버통신됨");
          alert("작성 완료 되었습니다!");
          this.$router.push("/reviewdetail/" + res.data);
        },
        error => {
          console.log(error);
        }
      );
    },
    filesave() {
      let data = new FormData();
      for (var i = 0; i < this.imageFiles.length; i++)
        data.append("files", this.imageFiles[i]);

      ReviewApi.uploadFiles(
        data,
        res => {
          for (var i = 0; i < res.data.length; i++) {
            this.imageNames.push(res.data[i]);
          }
          var index = 0;

          //대표글 변환
          let start = 0;
          let last = 0;
          if (typeof this.review.file != typeof "str") {
            let start = this.review.title.indexOf("data:image", 0);
            let last = this.review.title.indexOf('"', start + 1);
            let url = this.review.title.substring(start, last);
             this.review.title = this.review.title.replace(url, "");
            
            //start = this.review.title.indexOf("&quot;", 0);
            this.review.title = this.review.title.replace(
              "&quot;",
              `http://13.209.16.137:8080/upload/${this.imageNames[0]})`
            );
            this.review.backgroundImg = "";
            index++;
          }

          //본분 변환
          start = 0;
          let tmptext = this.review.content;
          while (start >= 0) {
            start = tmptext.indexOf("data:image", start + 1);
            if (start < 0) break;

            console.log("본분 파일 index:" + index);

            last = tmptext.indexOf('"', start);
            console.log("start:" + start + " last:" + last);

            var url = tmptext.substring(start, last);
            console.log(url);
            tmptext = tmptext.replace(
              url,
              `http://13.209.16.137:8080/upload/${this.imageNames[index]}`
            );
            index++;
          }

          this.$set(this.review, "content", tmptext);

          this.submit();
        },
        error => {
          console.log(error);
        }
      );
    },
    showImagePrompt(command) {
      this.comm = command;
      console.log(this.comm);
      this.$refs.image.click();
    },
    readURL(input) {
      var fileList = input.target.files;

      if (input.target.files && input.target.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          console.log(e.target);
          const src = e.target.result;
          this.comm({ src });
          //this.comm = "";
        };
        reader.readAsDataURL(fileList[0]);
      }
    },
  niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    }
  },
  beforeDestroy() {
    this.editor.destroy();
  }
};
</script>

<style >
/* .editor {
  max-width: 40rem;
} */
.editor__content ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}

.editor p.is-empty:first-child::before {
  content: attr(data-empty-text);
  float: left;
  color: darkgray;
  /* padding-left: 10px; */
  pointer-events: none;
  height: 0;
}

</style>