<template>
  <div class="bg-img">
    <navbar></navbar>
    <div class="containerC" style="margin-top: 40px;">
      <div>
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
      </div>
      <h1
        style="text-align: center; font-family:'MapoGoldenPier'; font-weight: bold; font-size: 40px;"
      >릴레이 소설 시작하기</h1>
      <!-- <hr style="border-top: 1px solid rgba(0, 0, 0, 0.2); margin: 30px auto;" /> -->
      <div class="c1">
        <!-- <div
          class="warning"
          style="font-size: 18px; text-align: center; color: crimson; margin: 3vh 8vw; background-color: rgba(255, 255, 255, 0.4)"
        >
          <p style="color: black;">안녕하세요, 책을 그리다 스케치북 입니다.</p>
          <p>⚠️ 주의 사항 ⚠️</p>
        </div>-->
        <h5>
          <b-badge
            pill
            v-b-popover.hover.right="'릴레이 횟수를 정해두고 횟수만큼 다른 사용자들과 함께 만들어가는 소설입니다. 공개 | 비공개 설정할 수 있으며 비공개시 원하는 사용자만 초대하여 함께 소설을 작성할 수 있습니다.'"
            title="스케치북 릴레이 소설 쓰기입니다.😊"
            href="#"
            variant="info"
          >릴레이 소설이란?</b-badge>
        </h5>
        <div class="canvas" style="margin-top: 20px;">
          <div class="relay-title-area">
            <input
              id="post_subject"
              name="subject"
              class="relay-title"
              type="text"
              value
              placeholder="제목"
              v-model="title"
            />

            <div class="relay-private" @click="isPublic=!isPublic">
              <img
                v-if="isPublic"
                style="height:32px;"
                src="@/assets/images/unlock.png"
                alt="unlock"
              />
              <img v-if="!isPublic" style="height: 32px;" src="@/assets/images/lock.png" alt="lock" />
            </div>
          </div>
          <!-- 비공개시 설정 -->
          <div v-if="!isPublic" class="settings row" style="vertical-align: middle; height: 74px;">
            <p
              style="margin: 0; vertical-align: middle; margin: auto; font-size:18px; color: rgba(0, 0, 0, 0.4); font-weight: bold;"
            >초대</p>
            <div
              class="input-group input-group-lg col-8"
              style="border-right: 1px solid rgba(128, 128, 128, 0.3);"
            >
              <vue-tags-input
                v-model="tag"
                :tags="tags"
                :autocomplete-items="filteredItems"
                @tags-changed="newTags => tags = newTags"
                placeholder="초대하고 싶은 사용자를 검색하세요."
                style="width: 100%; background: rgba(255, 255, 255, 0.5); border-radius: 10px;"
              />
            </div>
            <div class="col-3" style="text-align: center; justify-content:space-around;">
              <b-dropdown id="dropdown-1" variant="outline-dark" text="릴레이 횟수" :value="turn">
                <b-dropdown-item @click="turn=3">3</b-dropdown-item>
                <b-dropdown-item @click="turn=6">6</b-dropdown-item>
                <b-dropdown-item @click="turn=9">9</b-dropdown-item>
                <b-dropdown-item @click="turn=12">12</b-dropdown-item>
                <b-dropdown-item @click="turn=15">15</b-dropdown-item>
                <b-dropdown-item @click="turn=100000000">제한 없음</b-dropdown-item>
              </b-dropdown>
              <p v-if="turn<=15">
                <small>{{turn}}</small>
              </p>
              <p v-if="turn>15">
                <small>무한</small>
              </p>
            </div>
          </div>
          <!-- 공개시 설정 -->
          <div v-if="isPublic" class="settings row" style="vertical-align: middle; height: 74px;">
            <p
              style="margin: auto; vertical-align: middle; margin: auto; font-size:18px; color: rgba(0, 0, 0, 0.4); font-weight: bold;"
            >설정</p>
            <div
              class="input-group input-group-lg col-8"
              style="border-right: 1px solid rgba(128, 128, 128, 0.3); vertical-align:middle;"
            >
              <p style="margin: auto; color: rgba(0, 0, 0, 0.5);">전체 공개일 경우 모든 사용자가 소설을 작성할 수 있습니다.</p>
            </div>
            <div class="col-3" style="text-align: center; justify-content:space-around;">
              <b-dropdown id="dropdown-1" variant="outline-dark" text="릴레이 횟수">
                <b-dropdown-item @click="turn=3">3</b-dropdown-item>
                <b-dropdown-item @click="turn=6">6</b-dropdown-item>
                <b-dropdown-item @click="turn=9">9</b-dropdown-item>
                <b-dropdown-item @click="turn=12">12</b-dropdown-item>
                <b-dropdown-item @click="turn=15">15</b-dropdown-item>
                <b-dropdown-item @click="turn=100000000">제한 없음</b-dropdown-item>
              </b-dropdown>
              <p v-if="turn<=15">
                <small>{{turn}}</small>
              </p>
              <p v-if="turn>15">
                <small>무한</small>
              </p>
            </div>
          </div>
          <!-- <input type="text" placeholder="소설 제목을 입력하세요." style="padding: 0;" /> -->
          <br />
          <!-- <textarea name id cols="30" rows="10" placeholder="소설을 작성하세요."></textarea> -->
          <div class="fr-wrapper" dir="ltr" style="color: black;">
            <div
              class="relay-summary"
              dir="ltr"
              contenteditable="true"
              aria-disabled="false"
              spellcheck="true"
              @input="getDescInnerHtml"
            >
              <span
                v-if="description==''"
                class="fr-placeholder"
                style="font-size: 15px; color: rgba(0, 0, 0, 0.4); line-height: 24px; margin-top: 0px; padding-top: 0px; padding-left: 10px; margin-left: 0px; padding-right: 10px; margin-right: 0px; text-align: start;"
              >
                소설에 대한 설명을 적어주세요.
                <br />ex) 주인공, 장르 등
              </span>
            </div>
            <br />
            <div
              class="relay-content"
              dir="ltr"
              contenteditable="true"
              aria-disabled="false"
              placeholder="내용을 입력해주세요"
              spellcheck="true"
              @input="getInnerHtml"
            >
              <!-- <span
                class="fr-placeholder"
                style="font-size: 15px; color: rgba(0, 0, 0, 0.4); line-height: 24px; margin-top: 0px; padding-top: 0px; padding-left: 10px; margin-left: 0px; padding-right: 10px; margin-right: 0px; text-align: start;"
              >내용을 입력해주세요</span>

              <div
                class="kix-cursor docs-ui-unprintable"
                style="opacity: 1; left: 435px; top: 105px;"
              >
                <div
                  class="kix-cursor-caret"
                  style="border-color: rgb(255, 0, 122); height: 17.6px;"
                ></div>
                <div class="kix-cursor-top" style="background-color: rgb(255, 0, 122); opacity: 1;"></div>
                <div
                  class="kix-cursor-name"
                  style="opacity: 0; background-color: rgb(255, 0, 122); display: none;"
                >이채은</div>
              </div>-->
            </div>
          </div>
        </div>
        <br />
        <div class="buttons" style="text-align: right;">
          <button
            type="button"
            style="margin-right: 10px; font-size: 15px;"
            class="btn btn-sm btn-dark"
            @click="addRelay"
            id="btnSave"
          >소설 등록하기</button>

          <button
            type="button"
            style="font-size: 15px;"
            class="btn btn-sm btn-outline-dark"
            id="btnList"
            @click="gotoList"
          >목록</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script src="https://unpkg.com/@johmun/vue-tags-input/dist/vue-tags-input.js"></script>
<script>
import RelayApi from "@/apis/RelayApi";
import UserApi from "@/apis/UserApi";
import NotiApi from "@/apis/NotiApi";

import VueTagsInput from "@johmun/vue-tags-input";
import Navbar from "../../components/common/Navbarsub.vue";
import "../../assets/css/autosearch.css";
import $ from "jquery";

export default {
  components: {
    Navbar,
    VueTagsInput
  },
  data() {
    return {
      searchText: "",
      users: [],
      userId: "",
      userindexnum: 0,
      tag: "",
      tags: [],
      autocompleteItems: [],
      inputstory: "",
      description: "",
      title: "",
      turn: 6,
      relayId: 0,
      isPublic: true,
      relay: {},
      user: {}
    };
  },
  created() {
    this.userId = localStorage.getItem("userId");
    this.token = localStorage.getItem("JWT");
    if (this.userId != null) {
      UserApi.getUserbyUserId(
        this.userId,
        res => {
          console.log("로그인 되어있다");
          this.user = res.data;
        },
        error => {
          console.log("getUserbyUserId Error");
        }
      );
    }
  },
  mounted() {
    this.MainInit();
    this.getUsers();
  },
  computed: {
    filteredItems() {
      return this.autocompleteItems.filter(i => {
        return i.text.toLowerCase().indexOf(this.tag.toLowerCase()) !== -1;
      });
    }
  },
  methods: {
    MainInit() {
      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        console.log("Clicked menu");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },
    // moreUsers() {
    //   this.userindexnum++;
    //   this.search();
    // },
    getUsers() {
      this.users = [];
      // let { userindexnum } = this;
      // data = { searchText, userindexnum };
      let userId = localStorage.getItem("userId");
      RelayApi.getUsers(
        "",
        res => {
          for (let i = 0; i < res.data.length; i++) {
            if (userId == res.data[i].userId) continue;
            let data = {
              text: res.data[i].nickname,
              id: res.data[i].userId
            };
            this.autocompleteItems.push(data);
            this.users.push(res.data[i]);
          }
        },
        error => {
          this.$router.push("/Error");
        }
      );
    },
    getInnerHtml: function(e) {
      this.inputstory = e.target.innerHTML;
    },
    getDescInnerHtml: function(e) {
      console.log(e);
      this.description = e.target.innerHTML;
    },
    addRelay() {
      //릴레이등록  title, ispublic, description, inputstory, userId, tags
      let data = {
        title: this.title,
        remainTurn: this.turn,
        isPublic: this.isPublic,
        description: this.description,
        starter: localStorage.getItem("userId")
      };
      if (data.isPublic) data.isPublic = 1;
      else if (!data.isPublic) data.isPublic = 0;

      console.log(data);
      RelayApi.addRelay(
        data,
        res => {
          this.relayId = res.data.relaynovelId;
          this.relay = res.data;
          this.addRelayWriter(); //글 추가

          //this.tags.push({ text: "", id: data.starter });

          for (let i = 0; i < this.tags.length; i++) {
            this.addInvitation(this.tags[i].id);
          }
          this.addInvitation(data.starter);
        },
        error => {
          console.log("addRelay Error");
        }
      );
    },
    gotoList() {
      this.$router.push("/relay/relayboard");
    },
    addInvitation(userId) {
      let data = {
        relaynovelId: this.relayId,
        userId: userId
      };
      RelayApi.addInvitation(
        data,
        res => {
          console.log("Invitation Complete");
          // notification call ......
        },
        error => {
          console.log("addInvitation Error");
        }
      );
      let notification = {
        sendUserId: localStorage.getItem("userId"),
        receiveUserId: userId,
        notType: "invite",
        notContentId: this.relayId,
        notMessage: localStorage.getItem("nickname")+"님이 '"+ this.title+"'글에 초대를 했습니다.",
        notUrl: "http://13.209.16.137:8080/index.html#/user/profile/"+userId

      };
      if(notification.sendUserId!=notification.receiveUserId){
        NotiApi.addNoti(
          notification,
          res => {
            console.log("success add noti");
          },
          error => {
  
          }
        )
      }
    },
    addRelayWriter() {
      // relaynovelid, userid, turn, content, profileimg, nickname
      let data = {
        relaynovelId: this.relayId,
        userId: localStorage.getItem("userId"),
        turn: this.turn,
        content: this.inputstory,
        profileImg: this.user.profileImg,
        nickname: this.user.nickname
      };

      data.content = data.content
        .replace(/<div>/gi, "<br>")
        .replace(/<\/div>/gi, "");
      console.log(data.content);

      console.log("addrelaywriter data");
      console.log(data);
      RelayApi.addRelayWriter(
        data,
        res => {
          alert("작성이 완료 되었습니다.");
          //this.$router.push("/relay/relaydetail/" + this.relayId);
          this.$router.push({
            name: "RelayDetail",
            params: { relay: this.relay }
          });
        },
        error => {
          console.log("addRelayWriter Error");
        }
      );
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    }
  }
};
</script>
<style>
