<template>
<div>
  <navbar></navbar>
  <div id="reviewcreate" class="containerB">
    <div class="c4">
      <div>
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
      </div>
      
      <!-- 책 검색 -->

      <!-- <div class="d-flex justify-content-center">
          <div class="searchbar">
            <input v-model="keyword" class="search_input" type="text" name placeholder="Search..." />
            <button class="search_icon" v-on:click="Popup">
              <i class="fas fa-search"></i>
            </button>
          </div>
      </div>-->
      <div class="containerSearch">
        <input type="text" v-model="keyword" placeholder="Search..." @keyup.enter="Popup" />
        <div class="search"></div>
      </div>

      <book-components
        title="검색 결과"
        :keyword.sync="keyword"
        :visible.sync="visible"
        :item.sync="item"
        :items.sync="items"
        :index.sync="index"
      ></book-components>

      <!-- <input type="text" placeholder="검색 가능합니다"> -->
      <!-- <div class="search-wrapper">
      <div class="input-holder">
          <input type="text" class="search-input" placeholder="Type to search" />
          <button class="search-icon" onclick="searchToggle(this, event);"><span></span></button>
      </div>
    <span class="close" onclick="searchToggle(this, event);"></span>
      </div>-->

      <!-- 책 정보 -->
      <div class="book-info row" style="background: rgba(255, 255, 255, 0.1);">
        <div
          class="col-4 card-img-body"
          style="display: flex; align-items: center; justify-content: center;"
        >
          <img style="float:left; margin-top:5px;" v-bind:src="item.image" alt="사진" />
        </div>
        <div class="col-8 card-body">
          <p>제목 : {{item.title}}</p>
          <p>저자 : {{item.author}}</p>
          <!-- 별점 매기기 -->
          <div>
            <fieldset class="rating">
              <input type="radio" id="star5" name="rating" value="5" v-model="review.rating" />
              <label class="full" for="star5" title="Awesome - 5 stars"></label>
              <input type="radio" id="star4" name="rating" value="4" v-model="review.rating" />
              <label class="full" for="star4" title="Pretty good - 4 stars"></label>
              <input type="radio" id="star3" name="rating" value="3" v-model="review.rating" />
              <label class="full" for="star3" title="Meh - 3 stars"></label>
              <input type="radio" id="star2" name="rating" value="2" v-model="review.rating" />
              <label class="full" for="star2" title="Kinda bad - 2 stars"></label>
              <input type="radio" id="star1" name="rating" value="1" v-model="review.rating" />
              <label class="full" for="star1" title="Sucks big time - 1 star"></label>
            </fieldset>
          </div>
        </div>
      </div>

      <div class="priview-controller">
        <!-- 미리보기 폰트, 사이즈, 굵기 설정 -->
        <table style="width: 100%; margin: auto;">
          <tr>
            <td style="width:30%">
              <select v-model="selectedFont" style="width:90%;">
                <option v-for="font in fonts" v-bind:key="font.value">{{font.text}}</option>
              </select>
            </td>
            <td style="width:30%">
              <select v-model="selectedWeight" style="width:90%; float:center;">
                <option
                  v-for="weight in weights"
                  v-bind:key="weight.text"
                  v-bind:value="weight.value"
                >{{weight.text}}</option>
              </select>
            </td>
            <td style="width:30%">
              <div class="horizontally-stacked-slider" style="width:90%; float:center;">
                <span style="color:red;">{{size}}</span>
                <input type="range" v-model="size" value="25" min="10" max="100" />
              </div>
            </td>
            <td>
              <img
                style="width: 100%;"
                v-on:click="color = (color=='black')?'white':'black'"
                :src="require(`@/assets/images/ink-${color}.png`)"
              />
            </td>
          </tr>
        </table>
      </div>

      <!-- 미리보기 영역 -->

      <div ref="previewAreaWrapper">
        <div
          class="preview-area-wrapper"
          style="width:100%; height:450px;
            background-size: contain;
            background-position: center; margin: auto;"
          v-bind:style="{'background-image': 'url('+getImg(this.name)+')'}"
        >
          <!-- <img :src="openImg" /> -->
          <div
            class="preview-area"
            style="width: 100%; height: 100%; display: table; 
           table-layout: fixed;text-overflow: ellipsis;
            overflow: hidden;"
          >
            <p
              class
              contenteditable="true"
              style="text-align:center; weight:100%;height:100%; text-overflow:ellipsis; display:table-cell; vertical-align: middle;"
              v-bind:style="{ fontFamily:selectedFont, fontSize : size+'px', fontWeight: selectedWeight, color: color }"
              @input="getInnerHtml"
            ></p>
          </div>
        </div>
      </div>

      <background-component @send-name="changeBack" style="margin-top:10px;"></background-component>

      <div style="text-align:right; margin-bottom:50px;margin-right: 15px;">
        <b-btn @click="goToContent">본문적기</b-btn>
      </div>

      <!-- <div id="footer">
        <div id="foot_reviewcreate">
          <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet" />
          <p>
            <router-link v-bind:to="{name:'Main'}" class="div2">
              <button class="footer-btn">
                <i class="material-icons" id="foot_1">house</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'Search'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_2">search</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'ReviewCreate'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_3">add_circle_outline</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'Login'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_4">perm_identity</i>
              </button>
            </router-link>
          </p>
        </div>
      </div> -->
    </div>
  </div>
</div>

  
</template>

<script>
import "@/assets/css/layout.scss";
import "../../assets/css/review.scss";
import ReviewApi from "../../apis/ReviewApi";
import bookSearchModal from "./BookSearchModal.vue";
import BackgroundControl from "./BackgroundTab.vue";
import Navbar from "../../components/common/Navbarsub.vue";
import $ from "jquery";

export default {
  data: () => {
    return {
      thumbnail: "",
      selectedFont: "MapoGoldenPier",
      selectedWeight: "400",
      size: 40,

      fonts: [
        { text: "MapoGoldenPier", value: "1" },
        { text: "NotoSerifKR", value: "2" },
        { text: "RIDIBatang", value: "3" },
        { text: "Handon", value: "4" },
        { text: "Nanum Brush Script", value: "5" },
        { text: "Bangers", value: "6" },
        { text: "Black Han Sans", value: "7" },
        { text: "Caveat", value: "8" },
        { text: "Cute Font", value: "9" },
        { text: "Crimson Text", value: "10" },
        { text: "Dancing Script", value: "11" },
        { text: "Do Hyeon", value: "12" },
        { text: "East Sea Dokdo", value: "13" }
      ],
      weights: [
        { text: "Regular", value: "400" },
        { text: "Bold", value: "800" }
      ],

      keyword: "",
      title: "",
      author: "",
      visible: false, // 모달 on/off
      component: this,
      items: [],
      index: 1,
      item: {
        author: "",
        image: "",
        isbn: "",
        publisher: "",
        title: ""
      },
      image: "sketchbook",
      dataurl: "",
      name: "img_sketchbook_wide.jpg",
      review: {
        title: "",
        bookIsbn: "",
        userId: "",
        content: "",
        date: "",
        rating: 0,
        isreply: 0,
        backgroundImg: "",
        font: "",
        fontSize: "",
        fontColor: "",
        titleText: ""
      },
      color: "black"
    };
  },
  components: {
    bookComponents: bookSearchModal,
    BackgroundComponent: BackgroundControl,
    Navbar
  },
  created() {
    this.component = this;
  },
  watch: {
    name: function(v) {
      console.log("change name!");
      console.log(v);
    },
    index: function(v) {
      this.searchBook();
    }
  },
  mounted() {
    this.MainInit();
  },
  methods: {
    MainInit() {
      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },
    Popup() {

      this.visible = !this.visible;
      this.searchBook();
    },
    searchBook() {
      let { keyword, index } = this;
      let data = {
        keyword,
        index
      };
      data.index = (index - 1) * 10 + 1; // 1: (1-0)*10+1  2: (2-1)*10+1
      ReviewApi.searchBook(
        data,
        res => {
          //returns book vo list
          var list = [];
          list = res.data.object;

          var len = this.items.length;
          console.log("len:"+len);
          this.items.splice(0, len);
          for (var i = 0; i < list.length; i++) {
            this.items.push(list[i]);
          }
          
        },
        error => {
          this.index = 1;
          this.keyword = '';
          console.log(error);
        }
      );
    },
    getImg(img) {
      var tmp = img;
      return `http://13.209.16.137:8080/upload/${img}`;
    },
    fontcolorImg() {
      return `../../assets/images/ink-${this.color}.png`;
    },
    goToContent() {
      this.$set(this.review, "bookIsbn", this.item.isbn);
      this.$set(this.review, "userId", localStorage.getItem("userId"));
      //this.$set(this.review, "rating", 5);
      this.$set(this.review, "backgroundImg", this.name);
      this.$set(this.review, "font", this.selectedFont);
      this.$set(this.review, "fontSize", this.size);
      this.$set(this.review, "fontColor", this.selectedWeight);
      this.$set(this.review, "file", this.name);

      var title = this.$refs.previewAreaWrapper.innerHTML;
      /* if (typeof this.name != typeof "string") {
        let start = title.indexOf("data:image", 0);
        let last = title.indexOf('"', start + 1);
        let url = title.substring(start, last);
        title = title.replace(url, "");
      }
      title = title.replace("true", "false"); */
      title = title.replace("true", "false");
      this.$set(this.review, "title", title);
      this.$set(this.review, "titleText", this.review.titleText);
      //alert(this.review.titleText);
      var data = {
        bookIsbn: this.item.isbn,
        bookName: this.item.title,
        publisher: this.item.publisher,
        writer: this.item.author,
        img: this.item.image
      };
      ReviewApi.addBook(
        data,
        res => {
          console.log(res);
          this.$router.push({
            name: "ReviewCreate2",
            params: { review: this.review, item: this.item }
          });
        },
        error => {
          console.log(error);
        }
      );
    },
    changeBack(name) {
      this.name = name;
    },
    getInnerHtml: function(e) {
      this.review.titleText = e.target.innerHTML;
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    }
  }
};
</script>

