import http from "../http-common";

const getBookmarkByUser = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .get("/bookmark/getbookmark?userId=" + data)
        .then(Response => {
            console.log(data);
            callback(Response);
        })
        .catch(Error => {
            errorCallback(Error);
        })
}

const addBookmark = (data, callback, errorCallback) => {
    http
        .post("/bookmark/addBookmark", data)
        .then(Response => {
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log(Error);
            errorCallback(Error);

        })
}

const deleteBookmark = (data, callback, errorCallback) => {
    http({
        method: "delete",
        url: "http://localhost:8080/bookmark/deleteBookmark/",
        data: {
            userId: data.userId,
            reviewId: data.reviewId,
            date: data.date
        }
    }).then(response => {
        callback(response);
    }).catch(error => {
        errorCallback(error);
    });
}

const BookmarkApi = {
    getBookmarkByUser: (data, callback, errorCallback) => getBookmarkByUser(data, callback, errorCallback),
    addBookmark: (data, callback, errorCallback) => addBookmark(data, callback, errorCallback),
    deleteBookmark: (data, callback, errorCallback) => deleteBookmark(data, callback, errorCallback)
}


export default BookmarkApi