import First from './views/First.vue'
import Main from './views/Main.vue'
import Login from './views/user/Login.vue'
import Join from './views/user/Join.vue'
import ChangePassword from './views/user/ChangePassword.vue'
import UserDetail from './views/user/UserDetail.vue'
import UserUpdate from './views/user/UserUpdate.vue'
import EmailAuth from './views/user/EmailAuth.vue'
import Search from './views/Search.vue'
import ReviewCreate from './views/review/ReviewCreate.vue'
import ReviewCreate2 from './views/review/ReviewCreate2.vue'
import ReviewDetail from './views/review/ReviewDetail.vue'
import ReviewUpdate from './views/review/ReviewUpdate.vue'
import PageNotFound from './views/PageNotFound.vue'
import ErrorPage from './views/ErrorPage.vue'
import Components from './views/Components.vue'
import Profile from './views/user/Profile.vue'
import RelayBoard from './views/relay/RelayBoard.vue'
import RelayDetail from './views/relay/RelayDetail.vue'
import RelayCreate from './views/relay/RelayCreate.vue'

export default [{
    path: '/',
    name: 'First',
    component: First
},
{
    path: '/main',
    name: 'Main',
    component: Main
},
{
    path: '/user/login',
    name: 'Login',
    component: Login
},
{
    path: '/user/join',
    name: 'Join',
    component: Join
},
{
    path: '/user/changepassword',
    name: 'ChangePassword',
    component: ChangePassword
},
{
    path: '/user/userdetail',
    name: 'UserDetail',
    component: UserDetail
},
{
    path: '/user/userupdate/:user',
    name: 'UserUpdate',
    component: UserUpdate,
    props: true,
},
{
    path: '/user/emailauth/:user',
    name: 'EmailAuth',
    component: EmailAuth,
    props: true,
},
{
    path: '/search',
    name: 'Search',
    component: Search
},
{
    path: '/reviewcreate',
    name: 'ReviewCreate',
    component: ReviewCreate
},
{
    path: '/user/profile/:userId',
    name: 'Profile',
    component: Profile,
    props: true,
},
{
    path: '/reviewcreate2/:review/:item',
    name: 'ReviewCreate2',
    component: ReviewCreate2,
    props: true,
},
{
    path: '/reviewdetail/:reviewId',
    name: 'ReviewDetail',
    component: ReviewDetail,
    props: true,
},
{
    path: '/reviewupdate',
    name: 'ReviewUpdate',
    component: ReviewUpdate
},

{
    path: '/relay/relayboard',
    name: 'RelayBoard',
    component: RelayBoard
},

{
    path: '/relay/relaydetail/:relay',
    name: 'RelayDetail',
    component: RelayDetail,
    props: true
},

{
    path: '/relaycreate',
    name: 'RelayCreate',
    component: RelayCreate,
},
// {
//     path: '/relaynovel/:relaynovelId',
//     name: 'RelayNovel',
//     component: RelayNovel,
//     props: true,
// },
{
    path: '/error',
    name: 'Error',
    component: ErrorPage,
},
{
    path: '*',
    redirect: '/404',
},
{
    path: '/404',
    component: PageNotFound,
},

{
    path: '/components',
    name: 'Components',
    component: Components
},


]