const UserApi = {
    getNoti: (data, callback, errorCallback) => getNoti(data, callback, errorCallback),
    addNoti: (data, callback, errorCallback) => addNoti(data, callback, errorCallback),
    updateNoti: (data, callback, errorCallback) => updateNoti(data, callback, errorCallback),
}

