// * 가장 처음 뜨는 화면 (First.vue)
//   움직이는 구름 배경화면으로 신비로운 분위기 만들기
//   SketchBook의 인사말을 담은 책 열리는 효과 구현
//   책 안의 로고 이미지 클릭 시 메인 화면으로 이동
//   움직이는 글자 효과를 통해 독서 기록의 중요성 전달 및 스케치북만의 감성 전달
//   사용자에게 가장 먼저 보여주는 화면 --> 스케치북만의 이미지 잘 전달할 수 있도록 구현 필요
<template>
  <!-- <div class="stars"> -->
  <!-- <div class="twinkling"> -->
  <div class="clouds">
    <div class="user">
      <div class="wrapC">
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div class="card">
          <div class="imgBox">
            <div class="bark"></div>
            <img src="../assets/images/bookcover.png" />
          </div>
          <div class="details">
            <img
              @click="main"
              src="../assets/images/main_logo.png"
              alt="logo image"
              style="align: center; cursor: pointer; width: 200px; margin-left:50px; margin-top:-20px;"
            />
            <br />
            <br />
            <!-- <h4 class="color1">책을 그리다,</h4>
            <h4 class="color2 margin">스케치북</h4>-->

            <p class="first_card_content">안녕하세요 :)</p>
            <p>책을 그리다, 스케치북 입니다.</p>
            <p>한 권씩 쌓여가는 책들의 이야기,</p>
            <p>여기에 모아보는건 어떨까요?</p>
            <p>독서 일기, 그 기록하는 습관을</p>
            <p>스케치북이 함께 만들어 드릴게요!</p>
            <p class="text-right">Draw your books!</p>
            <p class="text-right">♥Sketchbook</p>
          </div>
        </div>
        <!-- 글자 바뀌는 부분 -->
        <div class="console-container">
          <span id="text"></span>
          <div class="console-underscore" id="console">&#95;</div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
  <!-- </div> -->
</template>
<script>
import "../assets/css/first.scss";
import "../assets/css/background.scss";
import Kakao from '../../kakao'
export default {
  data: () => {
    return {};
  },
  created() {
    Kakao.init('95b5fffd470fee85112efa7e53c29bca');
  },
  mounted() {
    this.consoleText(
      [
        "Sketch what you read.",
        "기록되지 않은 것은 기억되지 않는다.",
        "Life with Books.",
        "우리가 가진 유일한 인생은 일상이다."
      ],
      "text",
      ["black", "burlywood", "darkslategray", "dimgray", "saddlebrown"]
    );
  },
  methods: {
    consoleText(words, id, colors) {
      if (colors === undefined) colors = ["#fff"];
      var visible = true;
      var con = document.getElementById("console");
      var letterCount = 1;
      var x = 1;
      var waiting = false;
      var target = document.getElementById("text");
      target.setAttribute("style", "color:" + colors[0]);
      window.setInterval(function() {
        if (letterCount === 0 && waiting === false) {
          waiting = true;
          target.innerHTML = words[0].substring(0, letterCount);
          window.setTimeout(function() {
            var usedColor = colors.shift();
            colors.push(usedColor);
            var usedWord = words.shift();
            words.push(usedWord);
            x = 1;
            target.setAttribute("style", "color:" + colors[0]);
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (letterCount === words[0].length + 1 && waiting === false) {
          waiting = true;
          window.setTimeout(function() {
            x = -1;
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (waiting === false) {
          target.innerHTML = words[0].substring(0, letterCount);
          letterCount += x;
        }
      }, 120);
      window.setInterval(function() {
        if (visible === true) {
          con.className = "console-underscore hidden";
          visible = false;
        } else {
          con.className = "console-underscore";

          visible = true;
        }
      }, 400);
    },
    main() {
      this.$router.push("/main");
    }
  }
};
</script>