import http from "../http-common";

const getAllRelayIng = (data, callback, errorCallback) => {
    http
        .get("/relay/getAllRelayIng")
        .then(Response => {
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log(Error);
            errorCallback(Error);
        })
}

