    SearchRelay: (data, callback, errorCallback) => SearchRelay(data, callback, errorCallback),
    getContent: (data, callback, errorCallback) => getContent(data, callback, errorCallback),
    getUsers: (data, callback, errorCallback) => getUsers(data, callback, errorCallback),
    addRelay: (data, callback, errorCallback) => addRelay(data, callback, errorCallback),
    addInvitation: (data, callback, errorCallback) => addInvitation(data, callback, errorCallback),
    addRelayWriter: (data, callback, errorCallback) => addRelayWriter(data, callback, errorCallback),
    getReplyByRelayId : (data, callback, errorCallback) => getReplyByRelayId(data, callback, errorCallback),
    addRelayreply: (data, callback, errorCallback) => addRelayreply(data, callback, errorCallback),
    checkHeart:(data,callback,errorCallback) => checkHeart(data,callback, errorCallback),
    addRelayHeart:(data,callback,errorCallback) => addRelayHeart(data, callback, errorCallback),
