import http from "../http-common";

const getAllFollower = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .post("/follow/getAllFollower", data)
        .then(Response => {
            console.log(data);
            console.log("login success: " + Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })
}

const getAllFollowing = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .post("/follow/getAllFollowing", data)
        .then(Response => {
            console.log(data);
            console.log("login success: " + Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })
}

const addFollow = (data, callback, errorCallback) => {
    http
        .post("/follow/addFollow", data)
        .then(Response => {
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log(Error);
            errorCallback(Error);

        })
}

const deleteFollow = (data, callback, errorCallback) => {
    http({
        method:"delete",
        url:"http://localhost:8080/follow/deleteFollow/",
        data:{
            userOne: data.userOne,
            userTwo: data.userTwo,
            date: data.date
        }
    }).then(response=>{
        callback(response);
    }).catch(error=>{
        errorCallback(error);
    });
        
    // http
    //     .delete("/follow/deleteFollow", data)
    //     .then(Response => {
    //         console.log(Response);
    //         callback(Response);
    //     })
    //     .catch(Error => {
    //         console.log(Error);
    //         errorCallback(Error);

    //     })
}

const FollowApi = {
    getAllFollower: (data, callback, errorCallback) => getAllFollower(data, callback, errorCallback),
    getAllFollowing: (data, callback, errorCallback) => getAllFollowing(data, callback, errorCallback),
    addFollow: (data, callback, errorCallback) => addFollow(data, callback, errorCallback),
    deleteFollow: (data, callback, errorCallback) => deleteFollow(data, callback, errorCallback)
}


export default FollowApi