
<template>
  <!-- // * @author 하승진
// * @category layout.scss
// * @date 2020.02.06
// * 반응형 레이아웃으로 구성
//   화면 크기에 따라 로고, 멘트, 회원가입 입력 부분 크기 조절
//   메인 로고와 메인 멘트 넣고 효과 주기(글자 깜빡이는 blinking 효과)
// * 애니메이션 배경 추가 및 배경에 따른 구성요소 색상 변경
  //   글자 크기, 입력칸 크기 사이즈 조절-->

  <div class="bg-img">
    <div class="overlay">
      <div class="containerB">
        <div class="c1">
          <div class="c2">
            <img class="main_logo" src="../../assets/images/main_logo.png" />
          </div>
          <br />
          <br />
          <div class="c3">
            <h1 class="logo_ment">
              <span>책을 그리다,</span>
              <span>나만의 독서 일기</span>
              <span>
                <span class="blinking">스케치북</span>입니다.
              </span>
            </h1>
          </div>
        </div>
        <div class="c4">
          <h1 style="margin: 6vh 0 1vh; font-size: 30px; font-weight:bolder">비밀번호 변경</h1>
          <h3 class="small_ment" style="line-height:5vh; font-weight: bold;">
            <br />아이쿠,
            <br />가입할 때 사용하신 이메일로 변경링크 보내드릴게요.
          </h3>
          <br />
          <div class="input-with-label">
            <input
              v-model="email"
              v-bind:class="{error : error.email, complete:!error.email&&email.length!==0}"
              id="email"
              placeholder="이메일을 입력하세요."
              type="text"
            />

            <div class="error-text" v-if="error.email">{{error.email}}</div>
          </div>

          <button
            class="mybtn mybtn--back btn--login"
            v-on:click="sendEmail"
            :disabled="!isSubmit"
            :class="{disabled : !isSubmit}"
          >인증 메일 발송</button>
          <br />
          <br />
          <h6 style="margin-bottom:30px; color:#01A9DB;">{{msg}}</h6>

          <div class="add-option row">
            <div class="wrap" style="margin-top:30px; font-size: 19px;">
              <router-link
                v-bind:to="{name:'Login'}"
                class="btn--text"
                style="margin: 20px; 15px; float:left;"
              >← 로그인 화면</router-link>
              <div style="margin: 0 15px; float:right;">
                <span style="float:left;">
                  <b>이메일이 도착하지 않았다면</b>
                  <br />다시 이메일을 입력해
                  <br />요청을 눌러주세요
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/layout.scss";
import "../../assets/css/style.scss";
import "../../assets/css/user.scss";
import * as EmailValidator from "email-validator";
import UserApi from "../../apis/UserApi";

export default {
  data: () => {
    return {
      email: "",
      error: {
        email: false
      },
      isSubmit: false,
      reinput: false,
      msg: "",
      accent: false
    };
  },

  watch: {
    email: function(v) {
      this.checkForm();
    }
  },
  methods: {
    checkForm() {
      this.msg = "";
      if (this.email.length > 0 && !EmailValidator.validate(this.email)) {
        this.error.email = "이메일 형식이 아닙니다.";
      } else {
        this.error.email = false;
        this.isSubmit = true;
      }

      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
    },
    sendEmail() {
      this.msg = "메일을 발송하였습니다. 메일함에서 확인해주세요";
    }
  }
};
</script>
<style scoped>
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "NotoSerifKR";
}

.bg-img {
  background: center fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  border: 1px solid;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
  animation: backgroundmove 30s infinite;
  /* animation-name: backgroundmove;`
  animation-duration: 15s;
  animation-iteration-count: infinite; */
}

@keyframes backgroundmove {
  0% {
    background-image: url("../../assets/images/bg1.jpg");
  }
  25% {
    background-image: url("../../assets/images/bg2.jpg");
  }
  50% {
    background-image: url("../../assets/images/bg3.jpg");
  }
  75% {
    background-image: url("../../assets/images/bg4.jpg");
  }
  100% {
    background-image: url("../../assets/images/bg5.jpg");
  }
}

.overlay {
  background-color: rgba(255, 255, 255, 0.65);
  background-size: cover;
  border: 1px solid;
}
</style>