* @category Navbar add
* @date 2020.02.10
* 반응형 navbar 추가.

 사용시 해당뷰에서 Navbar import 후 아래 문단 추가.

    <script src="js/scripts.js"></script>
    <script src="https://ajax.googleapis.com/ajax/libs/jquery/3.1.1/jquery.min.js"></script>

    <script>
        import $ from 'jquery';

        $(window).ready(function() {
        $(window).scroll(function() {
            if ($(document).scrollTop() > 50) {
            $('.nav').addClass('affix');
            console.log("OK");
            } else {
            $('.nav').removeClass('affix');
            }
        });

        $(".navTrigger").click(function () {
            $(this).toggleClass('active');
            console.log("Clicked menu");
            $("#mainListDiv").toggleClass("show_list");
            $("#mainListDiv").fadeIn();
            });
        })
    </script>
-->
<template>
  <div>
    <nav class="nav_sub2">
      <div class="container_nav">
        <div class="logo">
          <router-link v-bind:to="{name:'Main'}">SketchBook</router-link>
        </div>
        <div id="mainListDiv" class="main_list">
          <ul class="navlinks">
            <!-- <li><router-link v-bind:to="{name:'Main'}">Home</router-link></li> -->
            <li>
              <router-link v-bind:to="{name:'Search'}">Search</router-link>
            </li>
            <li>
              <router-link v-bind:to="{name:'RelayBoard'}">Relay</router-link>
            </li>
            <li v-if="!isLogin">
              <router-link @click.native="writeClick()" v-bind:to="{name:'Login'}">Writing</router-link>
            </li>
            <li v-if="isLogin">
              <router-link v-bind:to="{name:'ReviewCreate'}">Writing</router-link>
            </li>
            <li v-if="!isLogin">
              <router-link v-bind:to="{name:'Login'}">Login</router-link>
            </li>
            <li v-if="isLogin">
              <router-link v-bind:to="'/user/profile/'+userId">Profile</router-link>
            </li>
            <li v-if="isLogin">
              <router-link @click.native="logout()" v-bind:to="{name:'Login'}">Logout</router-link>
            </li>
            <!-- <li v-if="isLogin" style="text-align:left;">
              
              <router-link @click.native="logout()" v-bind:to="{name:'Login'}">Logout</router-link>
            </li> -->
          </ul>
        </div>
        <span class="navTrigger">
          <i></i>
          <i></i>
          <i></i>
        </span>
        <span v-if="isLogin" @click="termPopup" style="cursor:pointer; color: white;" class="alarm_sub">
          🔔
          <terms-components title="알림" :isTerm.sync="isTerm" :visible.sync="visible"></terms-components>
        </span>
        
      </div>
    </nav>
    <!-- <section class="home"></section> -->
    <br />
    <br />
  </div>
</template>

<script>
import CarouselImg from "./CarouselImg";
import JoinTermModal from "./AlarmModal";

export default {
  name: "app",
  components: {
    termsComponents: JoinTermModal
  },
  data: () => {
    return {
      isLogin: false,
      userId: null,
      isTerm: false,
      visible: false
    };
  },
  created() {
    if (localStorage.getItem("userId") != null) {
      this.isLogin = true;
      this.userId = localStorage.getItem("userId");
    }
  },
  methods: {
    writeClick() {
      if (!this.isLogin) {
        alert("로그인하세요.");
        this.$router.push("/user/login");
      }
    },
    logout() {
      localStorage.clear();
      alert("안녕히가세요.");
    },
    termPopup() {
      this.visible = !this.visible;
    }
  }
};
</script>

<style>
.nav_sub2 {
  width: 100%;
  height: 50px;
  position: fixed;
  line-height: 50px;
  text-align: center;
  background-color: rgba(17, 17, 17, 0.753);
  z-index: 111;
  left: 0;
}

.nav_sub2 div.logo {
  /* float: left; */
  width: auto;
  height: auto;
  padding-left: 2rem;
}

.nav_sub2 div.logo a {
  text-decoration: none;
  color: #fff;
  font-size: 1.5rem;
  position: fixed;
  left: 2rem;
}

.nav_sub2 div.main_list {
  height: 50px;
  float: right;
  position: absolute;
  right: 40px;
}

.nav_sub2 div.main_list ul {
  width: 100%;
  height: 50px;
  display: flex;
  list-style: none;
  /* right: 20px; */
  margin: 0;
  padding: 0;
}

.nav_sub2 div.main_list ul li {
  width: auto;
  height: 50px;
  padding: 0;
  padding-right: 3rem;
}

.nav_sub2 div.main_list ul li a {
  text-decoration: none;
  color: #fff;
  line-height: 50px;
  font-size: 1rem;
}

.nav_sub2 div.main_list ul li a:hover {
  color: #f3e45e;
}

.home div {
  width: 100%;
  max-height: 45vh;
  background-position: center top;
  background-size: cover;
}

.navTrigger {
  display: none;
}

.nav_sub2 {
  /* padding-top: 20px; */
  z-index: 999;
  padding-bottom: 20px;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .container_nav {
    margin: 0;
  }
}

@media screen and (max-width: 768px) {
  .navTrigger {
    display: block;
  }

  .nav_sub2 div.logo {
    margin-left: 15px;
  }

  .nav_sub2 div.main_list {
    width: 100%;
    height: 0;
    overflow: hidden;
  }

  .nav_sub2 div.show_list {
    height: auto;
    display: none;
    right: 0;
  }

  .nav_sub2 div.main_list ul {
    flex-direction: column;
    margin-left: 65%;
    margin-top: 50px;
    width: 35%;
    height: 1000vh;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(17, 17, 17, 0.753);
    /*same background color of navbar*/
    background-position: center top;
  }

  .nav_sub2 div.main_list ul li {
    width: 100%;
    text-align: right;
  }

  .nav_sub2 div.main_list ul li a {
    text-align: center;
    width: 100%;
    font-size: 2rem;
    padding: 20px;
  }

  .nav_sub2 div.media_button {
    display: block;
  }
}

/* Animation */
/* Inspiration taken from Dicson https://codemyui.com/simple-hamburger-menu-x-mark-animation/ */
.alarm_sub{
  position: relative;
  margin: 0;
  right: -47%;
}

@-webkit-keyframes inM {
  50% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(45deg);
  }
}

@keyframes inM {
  50% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@-webkit-keyframes outM {
  50% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(45deg);
  }
}

@keyframes outM {
  50% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@-webkit-keyframes inT {
  0% {
    -webkit-transform: translateY(0px) rotate(0deg);
  }

  50% {
    -webkit-transform: translateY(9px) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(9px) rotate(135deg);
  }
}

@keyframes inT {
  0% {
    transform: translateY(0px) rotate(0deg);
  }

  50% {
    transform: translateY(9px) rotate(0deg);
  }

  100% {
    transform: translateY(9px) rotate(135deg);
  }
}

@-webkit-keyframes outT {
  0% {
    -webkit-transform: translateY(0px) rotate(0deg);
  }

  50% {
    -webkit-transform: translateY(9px) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(9px) rotate(135deg);
  }
}

@keyframes outT {
  0% {
    transform: translateY(0px) rotate(0deg);
  }

  50% {
    transform: translateY(9px) rotate(0deg);
  }

  100% {
    transform: translateY(9px) rotate(135deg);
  }
}

@-webkit-keyframes inBtm {
  0% {
    -webkit-transform: translateY(0px) rotate(0deg);
  }

  50% {
    -webkit-transform: translateY(-9px) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-9px) rotate(135deg);
  }
}

@keyframes inBtm {
  0% {
    transform: translateY(0px) rotate(0deg);
  }

  50% {
    transform: translateY(-9px) rotate(0deg);
  }

  100% {
    transform: translateY(-9px) rotate(135deg);
  }
}

@-webkit-keyframes outBtm {
  0% {
    -webkit-transform: translateY(0px) rotate(0deg);
  }

  50% {
    -webkit-transform: translateY(-9px) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-9px) rotate(135deg);
  }
}

@keyframes outBtm {
  0% {
    transform: translateY(0px) rotate(0deg);
  }

  50% {
    transform: translateY(-9px) rotate(0deg);
  }

  100% {
    transform: translateY(-9px) rotate(135deg);
  }
}

.affix {
  padding: 0;
  background-color: rgba(17, 17, 17, 0.753);
  height: 50px;
  line-height: 50px;
}

.myH2 {
  text-align: center;
  font-size: 4rem;
}

.myP {
  text-align: justify;
  padding-left: 15%;
  padding-right: 15%;
  font-size: 20px;
}

@media all and (max-width: 700px) {
  .myP {
    padding: 2%;
  }
}
</style>