/* 
  @author Chaeeun
  @category review create
 */

<template>
  <div>
    <div class="tab-wrap" style="display:tagle-row;">
      <!-- 배경색 tab들 넣을것 -->

      <div class="w3-row-padding">
        <div class="w3-col s3 w3-container">
          <a
            href="javascript:void(0)"
            class="w3-hover-opacity"
            v-on:click="send('img_sketchbook_wide.jpg')"
          >
            <img src="@/assets/images/img_sketchbook.jpg" alt="Nature" style="width:100%" />
          </a>
        </div>
        <div class="w3-col s3 w3-container">
          <a
            href="javascript:void(0)"
            class="w3-hover-opacity"
            v-on:click="send('img_snow_wide.jpg')"
          >
            <img src="@/assets/images/img_snowtops.jpg" alt="Snow" style="width:100%" />
          </a>
        </div>
        <div class="w3-col s3 w3-container">
          <a
            href="javascript:void(0)"
            class="w3-hover-opacity"
            v-on:click="send('img_pinkwoodtexture.jpg')"
          >
            <img src="@/assets/images/img_pinkwoodtexture.jpg" alt="Mountains" style="width:100%" />
          </a>
        </div>
        <div class="w3-col s3 w3-container">
          <a href="javascript:void(0)" class="w3-hover-opacity" v-on:click="uploadPopUp(this);">
            <img src="../../assets/images/file_add_icon.png" alt="Lights" style="width:70%" />
          </a>
        </div>
      </div>
      <br />
    </div>

    <!-- 사진 업로드 -->
    <div v-if="visible">
      <div class="file-upload" style="width: auto;">
        <button
          class="file-upload-btn"
          type="button"
          onclick="$('.file-upload-input').trigger( 'click' )"
        >Add Image</button>

        <div class="image-upload-wrap">
          <input class="file-upload-input" type="file" @change="readURL" accept="image/*" />
          <div class="drag-text">
            <h3>Drag and drop a file or select add Image</h3>
          </div>
        </div>
        <div class="file-upload-content">
          <img class="file-upload-image" src="#" alt="your image" />
          <div class="image-title-wrap">
            <button type="button" @click="removeUpload()" class="remove-image">
              Remove
              <!-- <span class="image-title">Uploaded Image</span> -->
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import $ from "jquery";

export default {
  data: () => {
    return {
      name: "",
      visible: false
    };
  },
  mounted() {},
  watch: {
    name: function(v) {
      this.$emit("send-name", v);
    }
  },
  methods: {
    send(str) {
      //this.name = `@/assets/images/img_${str}_wide.jpg`;
      this.name = str;
      this.$emit("send-name", this.name);
    },
    uploadPopUp() {
      this.visible = !this.visible;
    },
    readURL(input) {
      var fileList = input.target.files;
      var result = "";

      if (input.target.files && input.target.files[0]) {
        var reader = new FileReader();
        reader.onload = function(e) {
          $(".preview-area-wrapper").css(
            "background-image",
            "url(" + e.target.result + ")"
          );
          $(".image-upload-wrap").hide();
          $(".file-upload-image").attr("src", e.target.result);
          $(".file-upload-content").show();
        };
        reader.readAsDataURL(fileList[0]);
        this.$emit("send-name", fileList[0]);
      } else {
        this.removeUpload();
      }
    },
    removeUpload: function() {
      //$(".file-upload-input").replaceWith($(".file-upload-input").clone());
      $(".file-upload-input").val("");
      $(".file-upload-content").hide();
      $(".image-upload-wrap").show();
      $(".preview-area-wrapper").css(
        "background-image",
        "url(http://192.168.35.118:8080/static/img_sketchbook_wide.jpg)"
      );

      this.$emit("send-name", "img_sketchbook_wide.jpg");

      $(".image-upload-wrap").bind("dragover", function() {
        $(".image-upload-wrap").addClass("image-dropping");
      });
      $(".image-upload-wrap").bind("dragleave", function() {
        $(".image-upload-wrap").removeClass("image-dropping");
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.tab-wrap {
  display: table-row;
}
body {
  font-family: sans-serif;
  background-color: #eeeeee;
}

.file-upload {
  background-color: #ffffff;
  width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.file-upload-btn {
  width: 100%;
  margin: 0;
  color: #fff;
  background: #dc353594;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #15824b;
  transition: all 0.2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.file-upload-btn:hover {
  background: #1aa059;
  color: #ffffff;
  transition: all 0.2s ease;
  cursor: pointer;
}

.file-upload-btn:active {
  border: 0;
  transition: all 0.2s ease;
}

.file-upload-content {
  display: none;
  text-align: center;
}

.file-upload-input {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  outline: none;
  opacity: 0;
  cursor: pointer;
}

.image-upload-wrap {
  margin-top: 20px;
  border: 4px dashed #b21f1fcc;
  position: relative;
}

.image-dropping,
.image-upload-wrap:hover {
  background-color: #1fb264;
  border: 4px dashed #ffffff;
}

.image-title-wrap {
  padding: 0 15px 15px 15px;
  color: #222;
}

.drag-text {
  text-align: center;
}

.drag-text h3 {
  font-weight: 100;
  text-transform: uppercase;
  color: #15824b;
  padding: 60px 0;
}

.file-upload-image {
  max-height: 200px;
  max-width: 200px;
  margin: auto;
  padding: 20px;
}

.remove-image {
  width: 200px;
  margin: 0;
  color: #fff;
  background: #cd4535;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #b02818;
  transition: all 0.2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.remove-image:hover {
  background: #c13b2a;
  color: #ffffff;
  transition: all 0.2s ease;
  cursor: pointer;
}

.remove-image:active {
  border: 0;
  transition: all 0.2s ease;
}
</style>