<template>
  <button @click="toggleSwitch" :class="{active:isOn}" class="switch-component"></button>
</template>

<script>
export default {
  name: "switch",
  data: function() {
    return {
      isOn: false
    };
  },
  props: {
    initial: {
      type: Number,
      required: true
    }
  },
  mounted() {
    this.isOn = this.initial;
  },
  methods: {
    toggleSwitch() {
      this.isOn = !this.isOn;
      if (this.isOn == true) this.$emit("update:isOn", 1);
      else this.$emit("update:isOn", 0);
    }
  }
};
</script>
