
<template>
  <div class="modalB" v-if="visible" @click.self="handleWrapperClick">
    <div class="modal__dialog" style="width: 80vw; max-width: 800px; height: 88vh; margin: auto;">
      <header class="modal__header">
        <span>{{title}}</span>
        <span style="position: absolute; top: 2px; right: 2px;">
          <button @click="close" class="close">✖️</button>
        </span>
      </header>
      <hr />
      <div class="modal__body" style="height: 70vh">
        <!-- <ul class="relay-board-list" v-for="(relay,index) in relaysIng" v-bind:key="index"> -->
          <div class="review_box_search">
            <div v-for="(noti,index) in notis" v-bind:key="index">
              <!-- v-bind:style="{ fontFamily:selectedFont, fontSize : size+'px', fontWeight: selectedWeight, color: color }" -->
              <div class="row" style="margin-bottom:2px; background-color: #fff4a9;" v-if="noti.notReadDatetime == null" @click="readnoti(noti.notId)">
              <div class="col-3 card-img-body" style="height:7rem; display: flex; align-items: center; justify-content: center;" >
                <div class="v-avatar" style="height: 6rem; min-width: 6rem; width: 6rem;">
                  <img :src="noti.profileImg"
                       alt="Card image cap"
                       width="80vw"
                       border-radius="50%;"
                       height="120yh"/>
                </div>
              </div>
              <div class="col-6 card-body" style="height: 7rem; padding-top: unset;">
                <h4>
                  <span style="margin-left:unset; font-size: 0.9em; text-align: unset;">{{noti.nickname}}</span>
                </h4>
                <p>{{noti.notMessage}}</p>
              </div>
              <div class="col-3 card-body">
                
                <button @click="inviteNovel(noti.receiveUserId,noti.notContentId, noti.sendUserId, noti.title, noti.notReadDatetime)" v-if="noti.notType == 'inviterequest'">수락하기</button>
                <button @click="goToContent(noti.notUrl)" v-if="noti.notType == 'follow'">보러가기</button>
                <button @click="goToContent(noti.notUrl)" v-if="noti.notType == 'like'">게시글 보러가기</button>
              </div>
              </div>
              <div class="row" style="margin-bottom:2px;" v-if="noti.notReadDatetime != null" @click="readnoti(noti.notId)">
              <div class="col-3 card-img-body" style="height:7rem; display: flex; align-items: center; justify-content: center;" >
                <div class="v-avatar" style="height: 6rem; min-width: 6rem; width: 6rem;">
                  <img :src="noti.profileImg"
                       alt="Card image cap"
                       width="80vw"
                       border-radius="50%;"
                       height="120yh"/>
                </div>
              </div>
              <div class="col-6 card-body" style="height: 7rem; padding-top: unset;">
                <h4>
                  <span style="margin-left:unset; font-size: 0.9em; text-align: unset;">{{noti.nickname}}</span>
                </h4>
                <p>{{noti.notMessage}}</p>
              </div>
              <div class="col-3 card-body">
                
                <button @click="inviteNovel(noti.receiveUserId,noti.notContentId, noti.sendUserId)" v-if="noti.notType == 'inviterequest'">수락하기</button>
                <button @click="goToContent(noti.notUrl)" v-if="noti.notType == 'follow'">보러가기</button>
                <button @click="goToContent(noti.notUrl)" v-if="noti.notType == 'like'">게시글 보러가기</button>
              </div>
              </div>

            </div>

          </div>
      </div>
      <hr />
    </div>
  </div>
</template>

<script>
import NotiApi from "../../apis/NotiApi";
import RelayApi from "../../apis/RelayApi";
export default {
  name: "JoinTermModal",

  props: {
    visible: {
      type: Boolean,
      require: true,
      default: false
    },
    title: {
      type: String,
      require: false
    },
    isTerm: {
      type: Boolean,
      require: true
    }
  },
  methods: {
    checkForm() {
      this.checks = true;
      this.$emit("update:isTerm", this.checks);
      this.$emit("update:visible", false);
    },
    close() {
      this.$emit("update:visible", this.visible);
    },
    goToMain(){
      this.$router.push("/main");
    },
    goToContent(url){
      window.open(url,"_blank");
    },
    getNoti(){
      let receiveUserId = localStorage.getItem("userId");
      console.log(receiveUserId);
      NotiApi.getNoti(receiveUserId, res=>{
        this.notis=res.data;
        console.log(this.notis);
      });
    },
    readnoti(idx){
      let notification={
        notId: idx
      };
      NotiApi.updateNoti(notification,res=>{
        console.log("notichange!!");
        this.getNoti();
      });  
    },
    inviteNovel(receiveUserId,notContentId, sendUserId, title, notReadDatetime){
      if(notReadDatetime!=null) return;  //중복방지
      let data = {
        relaynovelId: notContentId,
        userId: sendUserId
      };
      console.log("invite test");
      console.log(data);
      RelayApi.addInvitation(
        data,
        res => {
          console.log("Invitation Complete");
          // notification call ......
        },
        error => {
          console.log("addInvitation Error");
        }
      );
      let relaynovel = null;
      RelayApi.getReplyByRelayId(notContentId,res=>{
        relaynovel = res;
        console.log(relaynovel);
        console.log("@@##");
      })
      let notification = {
        sendUserId: localStorage.getItem("userId"),
        receiveUserId: sendUserId,
        notType: "invite",
        notContentId: notContentId,
        notMessage: localStorage.getItem("nickname")+"님이 릴래이 소설 초대를 수락했습니다."
        // notUrl: "http://localhost:8081/#/user/profile/"+userId

      };
        NotiApi.addNoti(
          notification,
          res => {
            console.log("success add noti");
            alert("수락했습니다.");
          },
          error => {
  
          }
        )
    }
  },
  created(){
    console.log("created");
    this.getNoti();
  },

  data: function() {
    return {
      checks: this.isTerm,
      notis:[]
    };
  }
};
</script>
<style>
  .row:hover{
    background-color: whitesmoke;
  }
</style>