var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return (_vm.visible)?_c('div',{staticClass:"modalB",on:{"click":function($event){if($event.target !== $event.currentTarget){ return null; }return _vm.handleWrapperClick($event)}}},[_c('div',{staticClass:"modal__dialog",staticStyle:{"width":"80vw","max-width":"800px","height":"88vh","margin":"auto"}},[_c('header',{staticClass:"modal__header"},[_c('span',[_vm._v(_vm._s(_vm.title))]),_c('span',{staticStyle:{"position":"absolute","top":"2px","right":"2px"}},[_c('button',{staticClass:"close",on:{"click":_vm.close}},[_vm._v("✖️")])])]),_c('hr'),_c('div',{staticClass:"modal__body",staticStyle:{"height":"70vh"}},[_c('br'),_vm._v("SketchBook에 오신 것을 환영합니다! 본 약관은 회원님의 SketchBook 이용에 적용되며, 아래 설명된 SketchBook 서비스에 관한 정보를 제공합니다. 회원님이 SketchBook 계정을 만들거나 SketchBook을 이용하면 회원님은 본 약관에 동의하는 것입니다. "),_c('br'),_c('br'),_vm._m(0),_vm._v("당사는 회원님에게 SketchBook 서비스를 제공하는 것에 동의합니다. 서비스에는 당사가 SketchBook의 사명을 실현하기 위해 제공하는 모든 SketchBook 제품, 기능, 앱, 서비스, 기술 및 소프트웨어가 포함됩니다. 회원님이 관심 있는 사람들과 항목을 더 가까이 접할 수 있도록 서비스는 다음과 같은 요소(서비스)로 구성됩니다. "),_c('br'),_vm._v("- 창작, 연결, 커뮤니케이션, 발견 및 공유를 위한 맞춤화된 기회를 제공합니다. 사람들은 다양합니다. 당사는 회원님이 실제로 관심을 가지는 경험을 공유함으로써 관계를 강화하길 원합니다. 따라서 당사는 회원님 및 다른 사람들이 관심 있는 사람과 사항을 파악하는 시스템을 구축하고 파악한 정보를 이용함으로써 회원님에게 중요한 경험을 창작하고, 발견하며, 동참하고, 공유하도록 합니다. 그 일환으로, 회원님 및 다른 사람들이 SketchBook 안팎에서 하는 활동에 기초하여 회원님이 관심을 가질 만한 콘텐츠, 기능, 제안 및 계정을 더 잘 보여드리고, 회원님에게 SketchBook을 경험하는 방법을 제안하기도 합니다. "),_c('br'),_vm._v("- 긍정적이고 포괄적이며 안전한 환경을 조성합니다. 당사는 SketchBook 커뮤니티 회원들이 도움을 필요로 한다고 생각하는 경우를 포함하여 회원들의 경험이 긍정적이고 포괄적일 수 있도록 도구를 개발하고 사용하고, 자원을 제공합니다. 또한 당사는 SketchBook 약관 및 정책의 남용 및 위반을 포함하여 유해하고 사기적인 행위를 방지하기 위한 팀과 시스템을 갖추고 있습니다. 당사는 SketchBook 플랫폼의 보안을 유지하기 위해 당사가 보유한 모든 정보(회원님의 정보 포함)를 이용합니다. 또한 당사는 오용 또는 유해 콘텐츠에 관한 정보를 다른 계열사나 법 집행 기관과 공유할 수 있습니다. "),_c('br'),_vm._v("- 성장하고 있는 SketchBook 커뮤니티에 지속적으로 서비스를 제공하는 데 도움이 되는 기술을 개발하고 사용합니다. 성장하고 있는 SketchBook 커뮤니티를 위해 정보를 구성하고 분석하는 것이 SketchBook 서비스의 핵심입니다. SketchBook 서비스에서 큰 부분을 차지하는 것은 광범위한 글로벌 커뮤니티를 위하여 엄청나게 큰 규모로 SketchBook 서비스를 맞춤화하고, 보호하며, 향상시키는 데 도움이 되는 첨단 기술을 개발하고 사용하는 것입니다. 인공 지능 및 머신 러닝과 같은 기술은 저희가 SketchBook 서비스 전체에 복잡한 프로세스를 적용할 수 있게 합니다. 자동화 기술 또한 SketchBook 서비스의 기능성과 무결성 보장에 기여합니다. "),_c('br'),_vm._v("- SketchBook 계열사 제품 전반에 걸쳐 일관되고 원활한 경험을 제공합니다. SketchBook은 우수하고 안전하며 보안이 강화된 서비스를 제공하기 위해 기술, 시스템, 인사이트 및 정보를 공유합니다. 그 정보에는 당사가 보유하는 회원님에 대한 정보가 포함됩니다. 또한 회원님이 사용하는 SketchBook 계열사 제품과 상호 작용할 수 있는 방법을 제공하며, 회원님이 SketchBook 계열사 제품 전반에서 일관적이고 원활한 경험을 얻을 수 있도록 하는 시스템을 설계했습니다. "),_c('br'),_vm._v("- SketchBook 서비스를 위해 안정적인 글로벌 인프라를 보장합니다. 글로벌 서비스를 제공하기 위해 당사는 회원님의 거주 국가 이외의 지역을 비롯하여 전 세계에 있는 시스템에 데이터를 저장하고 전송해야 합니다. 이 인프라는 소유하거나 운영할 수 있습니다. "),_c('br'),_vm._v("- 회원님이 관심 있는 방식으로 회원님과 브랜드, 제품 및 서비스를 연결합니다. 당사는 회원님에게 의미있다고 생각되는 광고, 제안 및 기타 홍보 콘텐츠를 보여드리기 위해 SketchBook 및 다른 SketchBook 계열사 제품의 정보 외에도 제삼자의 정보를 이용합니다. 그리고 이러한 콘텐츠가 회원님이 SketchBook에서 경험하는 다른 모든 경험과 마찬가지로 관련성 있도록 노력합니다. "),_c('br'),_vm._v("- 연구 및 혁신. 당사는 보유한 정보를 이용하여 SketchBook 서비스를 연구하고, 다른 사람들과 협력하여 보다 나은 SketchBook 서비스를 만들고 SketchBook 커뮤니티의 복지에 기여하기 위한 연구를 수행합니다. "),_c('br'),_c('br'),_vm._m(1),_vm._v("SketchBook 서비스를 제공하기 위해서는 회원님의 정보를 수집하고 이용해야 합니다. 데이터 정책은 SketchBook 제품에서 저희가 정보를 수집, 이용하고 공유하는 방법에 대해 설명합니다. 또한 SketchBook 개인정보 보호 및 보안 설정을 포함해 회원님의 정보를 관리할 수 있는 여러 방법에 대해 설명합니다. SketchBook을 이용하려면 데이터 정책에 동의해야 합니다. "),_c('br'),_c('br'),_vm._m(2),_vm._v("SketchBook 서비스 제공에 대한 당사의 약속에 대해 회원님은 다음과 같은 약속을 해야 합니다. SketchBook을 이용할 수 있는 주체. 당사는 SketchBook 서비스가 가능한 개방적이고 포괄적인 서비스가 되기를 원하지만, 본 서비스가 안전하고, 보안성을 갖추며, 법을 준수하는 서비스가 되기를 원하기도 합니다. 따라서 회원님이 SketchBook 커뮤니티에 참여하려면 몇 가지 제한 사항을 준수해야 합니다. "),_c('br'),_vm._v("- 만 14세 이상이어야 합니다. "),_c('br'),_vm._v("- 관련 법률에 따라 SketchBook 서비스를 받는 것이 일부라도 금지된 사람 또는 관련 제재 대상 명단에 있어 결제 관련 SketchBook 서비스를 이용하는 것이 금지된 사람이 아니어야 합니다. "),_c('br'),_vm._v("- 과거에 당사가 회원님의 계정을 법률 또는 SketchBook 정책 위반을 이유로 하여 비활성화하지 않았어야 합니다. "),_c('br'),_vm._v("- 유죄가 확정된 성범죄자가 아니어야 합니다. "),_c('br'),_c('br'),_vm._m(3),_vm._v("광범위한 커뮤니티에 안전하고 개방적인 SketchBook 서비스가 제공되기 위해서는 우리 모두가 각자의 본분을 다해야 합니다. "),_c('br'),_vm._v("- 다른 사람을 사칭하거나 부정확한 정보를 제공하면 안 됩니다. SketchBook에서 회원님의 신원을 공개할 필요는 없지만 정확한 최신 정보(등록 정보 포함)를 제공해야 합니다. 또한 본인이 아닌 다른 사람을 사칭해서는 안 되며, 다른 사람의 명시적인 허락 없이 다른 사람의 이름으로 계정을 만들면 안 됩니다. "),_c('br'),_vm._v("- 불법적이거나, 오해의 소지가 있거나, 사기적인 행위 또는 불법적이거나 허가받지 않은 목적을 위한 어떠한 행위도 하면 안 됩니다. "),_c('br'),_vm._v("- 특히 본 약관 또는 저희 정책을 위반하거나 다른 사람들이 위반하도록 돕거나 조장해서는 안 됩니다. 행위 또는 콘텐츠를 신고하는 방법은 고객 센터에서 알아보세요. "),_c('br'),_vm._v("- SketchBook 서비스의 정상적인 운영을 방해하거나 지장을 주는 어떠한 일도 해서는 안 됩니다. "),_c('br'),_vm._v("- 허가받지 않은 방법으로 계정을 만들거나 정보에 접근하거나 정보를 수집하려 해서는 안 됩니다. 여기에는 당사의 명시적 허락 없이 자동화된 방법으로 계정을 만들거나 정보를 수집하는 것이 포함됩니다. "),_c('br'),_vm._v("- 회원님의 계정 일부(사용자 이름 포함)를 구입, 판매 또는 양도하거나, 다른 사용자의 로그인 정보 또는 배지를 요청, 수집 또는 사용하려고 시도해서는 안 됩니다. "),_c('br'),_vm._v("- 개인 정보 또는 기밀 정보를 게시하거나 지적 재산을 포함한 타인의 권리를 침해하는 어떠한 행위도 해서는 안 됩니다. 회원님의 지적 재산권을 침해한다고 생각되는 콘텐츠를 신고하는 방법을 포함한 자세한 내용을 알아보세요. "),_c('br'),_vm._v("- 당사의 사전 서면 동의 없이 회원님의 사용자 이름에 도메인 이름이나 URL을 사용하면 안 됩니다. 회원님이 당사에 부여한 권한. 본 계약의 일부로서, 회원님은 당사가 회원님에게 SketchBook 서비스를 제공하기 위해 필요로 하는 권한을 함께 부여합니다. "),_c('br'),_vm._v("- 당사는 회원님의 콘텐츠에 대한 소유권을 주장하지 않지만 회원님은 당사에 회원님의 콘텐츠를 이용할 라이선스를 부여합니다. 회원님의 콘텐츠에 대한 회원님의 권리에 있어 변경되는 사항은 없습니다. 당사는 회원님이 SketchBook 서비스에 또는 SketchBook 서비스를 통해 게시하는 콘텐츠에 대해 소유권을 주장하지 않습니다. 그 대신 회원님이 지적 재산권(사진 또는 동영상 등)이 적용되는 콘텐츠 또는 당사 서비스와 관련된 콘텐츠를 공유, 게시 또는 업로드할 때 회원님은 콘텐츠를 전 세계적으로 호스팅, 사용, 배포, 수정, 실행, 복사, 공개적으로 수행 또는 표시, 번역 및 파생 저작물을 생성할 수 있는 비독점적이고 양도 가능하며 2차 허가권을 가질 수 있고 사용료가 없는 허가권을 당사에 부여합니다(회원님의 개인정보 취급방침 및 애플리케이션 설정과 일치함). 이러한 라이선스는 SketchBook 서비스에 접속하고 이를 이용하는 회원님 및 다른 사람들에게 SketchBook 서비스 이용을 가능하게 하기 위한 것입니다. 콘텐츠 또는 계정을 삭제하여 언제든지 이러한 허가권을 종료할 수 있습니다. 단, 회원님의 콘텐츠를 공유한 타인이 해당 콘텐츠를 삭제하지 않았을 경우에는 해당 콘텐츠가 계속 보일 수 있습니다. "),_c('br'),_vm._v("- 회원님의 사용자 이름, 프로필 사진 및 회원님의 관계 및 행동에 관한 정보를 계정, 광고 및 홍보 콘텐츠에 이용할 수 있는 권한. 회원님은 당사에 회원님의 사용자 이름, 프로필 사진 및 회원님의 활동(예: \"좋아요\") 또는 관계(예: \"팔로우\")에 관한 정보를 SketchBook 제품 전반에 표시되는 계정, 광고, 제안 및 회원님이 팔로우하거나 참여하는 기타 홍보 콘텐츠와 함께 또는 이와 관련하여 표시할 수 있는 권한을 무상으로 부여합니다. 예를 들어, 당사는 회원님이 Sketch