<template>
<div class="modalB" v-if="visible" @click.self="handleWrapperClick">
  <div class="modal__dialog" style="width: inherit;">
    <div class="comments">
		<div class="comment-wrap" v-if="userId!=''">
		<div class="photo">
			<div class="avatar" v-bind:style="{backgroundImage: 'url('+user.profileImg+')'}"></div>
				</div>
				<div class="comment-block">
						<form action="">
							<textarea v-model="comment" name="" id="" cols="30" rows="3" placeholder="Add comment..."></textarea>
              <button @click="addReply" type="button" class="btn--text" style="float:right;">답글</button>
						</form>
				</div>
		</div>

    <div class="comment-wrap" v-if="userId==''">
      <span>로그인 하러 갈까요?</span>
      <router-link style="color:blue;" v-bind:to="{name:'Login'}" class="btn--text">로그인하기</router-link>
    </div>


		<div class="comment-wrap" v-for="(reply, index) in replys" v-bind:key="index">
				<div class="photo">
						<div class="avatar" v-bind:style="{backgroundImage: 'url('+reply.profileImg+')'}"></div>
				</div>
				<div class="comment-block">
						<p class="comment-text"><b>{{reply.comment}}</b></p>
						<div class="bottom-comment">
								<div class="comment-date">{{reply.createTime}}</div>
						</div>
				</div>
		</div>

    

    <div class="modal__footer btns-small"  style="background: none;">
        <button
          @click="$emit('update:visible', !visible)"
          class="close"
          style="margin-right:20px;" 
        >닫기</button>
      </div>

    
    </div>
    
    
  </div>
</div>  
</template>

<script>
import UserApi from "../../apis/UserApi";
import RelayApi from "../../apis/RelayApi";

    export default {
        props: {
            visible: {
            type: Boolean,
            require: true,
            default: false
            },
            relaynovelId:{
              type : Number,
              require: true
            }
        },
        data:()=>{
          return {
            userId:'',
            user:{},
            replys:[],
            comment:''
          }

        },
        mounted(){
          this.userId = localStorage.userId;

          if(this.userId != ''){
            UserApi.getUserbyUserId(
              this.userId,
              res=>{
                this.user = res.data;
              },
              error=>{
                console.log("getUserbyUserId Error!");
              }
            )
          }

          RelayApi.getReplyByRelayId(
            this.relaynovelId,
            res=>{
              for(let i=0; i<res.data.length; i++){
                this.replys.push(res.data[i]);
                this.getUserInfo(res.data[i].userId,i);
              }
            },
            error=>{

            });
        },
        methods:{
          handleWrapperClick(){
            this.$emit("update:visible", false);
          },
          getUserInfo(userId,index){
            UserApi.getUserbyUserId(
              this.userId,
              res=>{
                this.$set(this.replys[index],"profileImg",res.data.profileImg);
                this.$set(this.replys[index],"nickname",res.data.nickname);
              },
              error=>{
                console.log("getUserInfo Error!");
              }
            )
          },
          addReply(){
            let data = {
              userId : this.userId,
              relaynovelId : this.relaynovelId,
              createTime : '',
              comment : this.comment
            }
            RelayApi.addRelayreply(
              data,
              res=>{
                res.data.createTime = "just now..";
                res.data.profileImg = this.user.profileImg;
                //this.replys.push(res.data);

                //swap
                let tmp = this.replys[0];
                this.$set(this.replys, 0, res.data);
                this.replys.push(tmp);

                this.comment='';
              },
              error=>{
                console.log("addRelayreply Error");
              });
          }
        }
    }
</script>

<style lang="scss" scoped>
//@import "bourbon";

$ptsans: "PT Sans", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif;
$placeholder: #ced2db;
$text-color: #555f77;
$links: #0095ff;

.modal__dialog {
  background-color: #f0f2fa;
  //background-color:rgba(0, 0, 0, 0.6);
  font-family: $ptsans;
  color: $text-color;
  -webkit-font-smoothing: antialiased;
  overflow-y: scroll;
}

input, textarea {
  outline: none;
  border: none;
  display: block;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	//font-family: $ptsans;
	font-size: 16px;
  color: $text-color;
  /* @include placeholder {
		color: $placeholder;
	} */
}

p {
  line-height: 21px;
}

.comments {
  margin: 40px auto 0;
  max-width: 972px;
  padding: 0 20px;
}

.comment-wrap {
  margin-bottom: 20px;
  display: table;
  width: 100%;
  min-height: 85px;
}

.photo {
  //background-color: #ff6666;
  padding-top: 10px;
  display: table-cell;
  width: 56px;
  
  .avatar {
    //@include size(rem(36));
    width:3em;
    height:3em;
    //background-color: #0094ff;
    border-radius: 50%;
    background-size: contain;
  }
}

.comment-block {
  padding: 16px;
  background-color: #fff;
  display: table-cell;
  vertical-align: top;
  border-radius: 3px;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.08);
  
  textarea {
    width: 100%;
		resize: none;
  }
}

.comment-text {
  margin-bottom: 20px;
}

.bottom-comment {
  color: #acb4c2;
  font-size: 14px;
}

.comment-date {
  float: left;
}

.comment-actions {
  float: right;
  
  li {
    display: inline;
		margin: -2px;
		cursor: pointer;
    
    &.complain {
      padding-right: 12px;
      border-right: 1px solid #e1e5eb;
    }
    
    &.reply {
      padding-left: 12px;
			padding-right: 2px;
    }
		
		&:hover {
      color: $links;
			//text-decoration: underline;
    }
  }
}


</style>