<script>
import "@/assets/css/layout.scss";
import MainpageApi from "../apis/MainpageApi";
import ReviewApi from "../apis/ReviewApi";
import Navbar from "../components/common/Navbar.vue";
import CarouselImg from "../components/common/CarouselImg.vue";
import $ from "jquery";

export default {
  components: {
    Navbar,
    // CarouselImg,
  },
  data: () => {
    return {
      reviews: [],
      haertcnt: [],
      replycnt: [],
      date: ""
    };
  },
  created() {},
  mounted() {
    this.search();
    this.MainInit();
  },
  methods: {
    MainInit() {
      $(window).scroll(function() {
        if ($(document).scrollTop() > 50) {
          $(".nav_sub").addClass("affix");
          console.log("OK");
        } else {
          $(".nav_sub").removeClass("affix");
        }
      });

      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        console.log("Clicked menu");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },
    search() {
      let user = localStorage.getItem("userId");
      let data = {
        userId: user
      };
      MainpageApi.requestMainpage(
        data,
        res => {
          console.log(res);
          for (var i = 0; i < res.data.length; i++) {
            this.reviews.push(res.data[i]);
            this.getCount(res.data[i].reviewId, i);
            this.$set(this.reviews[i], "date", this.getDate(res.data[i].date));
          }
        },
        error => {
          this.$router.push("/Error");
        }
      );
    },
    getCount(reviewId, index) {
      ReviewApi.getCount(
        reviewId,
        res => {
          //this.haertcnt.push(res.data[0]);
          this.$set(this.reviews[index], "heartcnt", res.data[0]);
          //this.replycnt.push(res.data[1]);
          this.$set(this.reviews[index], "replycnt", res.data[1]);
        },
        error => {
          this.$$router.push("/Error");
        }
      );
    },
    getDate(date) {
      var d = new Date();
      //var dd = d.getDate();
      //console.log(dd);
      let ryear = date.substring(0, 4);
      let rmonth = date.substring(5, 7);
      let rday = date.substring(8, 10);
      let rhour = date.substring(11, 13);
      let rmin = date.substring(14, 16);

      let nyear = d.getFullYear();
      let nmonth = d.getMonth() + 1;
      nmonth = nmonth.length == 1 ? "0" + nmonth : nmonth;
      let nday = d.getDate();
      nday = nday.length == 1 ? "0" + nday : nday;
      let nhour = d.getHours();
      nhour = nhour.length == 1 ? "0" + nhour : nhour;
      let nmin = d.getMinutes();
      nmin = nmin.length ==1 ? "0"+nmin : nmin;

      console.log(
        "리뷰 시점" + ryear + " " + rmonth + " " + rday + " " + rhour
      );
      console.log(
        "현재 시점" + nyear + " " + nmonth + " " + nday + " " + nhour
      );

      if (nyear != ryear) {
        return Number(nyear) - Number(ryear) + " years ago.."; //현재 - 리뷰
      }

      if (nmonth != rmonth) {
        return Number(nmonth) - Number(rmonth) + " months ago.."; //현재 - 리뷰
      }

      if (nday != rday) {
        return Number(nday) - Number(rday) + " days ago..";
      }

      if (nhour != rhour) {
        return Number(nhour) - Number(rhour) + " hours ago..";
      }

      if(nmin != rmin){
        return "few minutes ago..";
      }
    },
    goToProfile(userId) {
      this.$router.push("/user/profile/" + userId);
    },
    goToDetail(reviewId) {
      this.$router.push("/reviewdetail/" + reviewId);
    }
  }
};
</script>

