import http from "../http-common";
const searchBook = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .get("/getBookFromApi?keyword=" + data["keyword"] + "&index=" + data["index"])
        .then(Response => {
            console.log("login success: " + Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })
}

