<script>
import "../assets/css/banner.scss";
import "../assets/css/search.scss";
import SearchApi from "../apis/SearchApi";
import $ from "jquery";
import Navbar from "../components/common/Navbarsub.vue";

export default {
  components: {
    Navbar
  },
  data: () => {
    return {
      searchText: "",
      reviews: [],
      users: [],
      userId: "",
      token: "",
      reviewindexnum: 0,
      userindexnum: 0
    };
  },
  created() {
    this.userId = localStorage.getItem("userId");
    this.token = localStorage.getItem("JWT");
  },
  computed: {
    thumbnailStyle: function() {
      return {
        fontFamily: this.review.font,
        fontSize: this.review.fontSize + "px",
        fontWeight: this.review.fontColor,
        backgroundImage:
          "url(http://localhost:8081/static/" + this.review.backgroundImg + ")"
      };
    }
  },
  mounted() {
    this.MainInit();
  },
  methods: {
    MainInit() {
      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },

    deleteClear() {
      this.searchText = "";
      this.reviewindexnum = 0;
      this.userindexnum = 0;
    },
    logout() {
      alert("안녕히가세요.");
      localStorage.clear();
      location.reload();
    },
    isLogin() {
      if (this.token == null) {
        this.$router.push("/user/Login");
      } else {
        this.$router.push("/user/Profile");
      }
    },
    moreReviews() {
      this.reviewindexnum++;
      this.search();
    },
    moreUsers() {
      this.userindexnum++;
      this.search();
    },
    search() {
      this.reviews = [];
      if (this.searchText.length > 0) {
        // \가 들어오면 안돼! 에러 발생 --> 처리해주자
        // this.searchText.replaceAll("\\\\", "");

        let { searchText } = this;
        let { reviewindexnum } = this;
        let data = {
          searchText,
          reviewindexnum
        };
        SearchApi.requestReviewSearch(
          data,
          res => {
            if (res == null) {
              //모름
              this.searchText = "";
            }
            this.reviews = res.data;
            var regex = new RegExp(this.searchText, "g");

            for (let index = 0; index < this.reviews.length; index++) {
              this.reviews[index].bookName = this.reviews[
                index
              ].bookName.replace(
                regex,
                "<strong class='highlight'>" + this.searchText + "</strong>"
              );
              this.reviews[index].titleText = this.reviews[
                index
              ].titleText.replace(
                regex,
                "<strong class='highlight'>" + this.searchText + "</strong>"
              );
            }
          },
          error => {
            this.$router.push("/Error");
          }
        );
        this.users = [];
        let { userindexnum } = this;
        data = { searchText, userindexnum };
        SearchApi.requestUserSearch(
          data,
          res => {
            if (res == null) {
              //모름
              this.searchText = "";
            }
            this.users = res.data;

            var regex = new RegExp(this.searchText, "g");

            for (let index = 0; index < this.users.length; index++) {
              this.users[index].nickname = this.users[index].nickname.replace(
                regex,
                "<strong class='highlight'>" + this.searchText + "</strong>"
              );
            }
          },
          error => {
            this.$router.push("/Error");
          }
        );
      }
    },
    goToProfile(user) {
      this.$router.push("/user/profile/" + user.userId);
    },
    goToReview(reviewId) {
      this.$router.push("/reviewdetail/" + reviewId);
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    }
  }
};
</script>
