  @category 리뷰 상세보기
  @date 2020.02.10
 */
<template>
  <div>
    <navbar></navbar>
    <div class="containerB">
      
      <div class="c4" style="width: 100%; height: 450px;">
        <div>
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
        </div>
        <div v-html="review.title"></div>
      </div>

      <div class="c4">
        <div style="margin-bottom:30px; display: flow-root;text-align: right;">
          <div style="clear:both;">
            <div style="float:left; display: inline-flex; margin-top: 10px;">
              <fieldset class="rating">
                <input
                  disabled="true"
                  type="radio"
                  id="star5"
                  name="rating"
                  value="5"
                  v-model="review.rating"
                />
                <label class="full" for="star5" title="Awesome - 5 stars"></label>
                <input
                  disabled="true"
                  type="radio"
                  id="star4"
                  name="rating"
                  value="4"
                  v-model="review.rating"
                />
                <label class="full" for="star4" title="Pretty good - 4 stars"></label>
                <input
                  disabled="true"
                  type="radio"
                  id="star3"
                  name="rating"
                  value="3"
                  v-model="review.rating"
                />
                <label class="full" for="star3" title="Meh - 3 stars"></label>
                <input
                  disabled="true"
                  type="radio"
                  id="star2"
                  name="rating"
                  value="2"
                  v-model="review.rating"
                />
                <label class="full" for="star2" title="Kinda bad - 2 stars"></label>
                <input
                  disabled="true"
                  type="radio"
                  id="star1"
                  name="rating"
                  value="1"
                  v-model="review.rating"
                />
                <label class="full" for="star1" title="Sucks big time - 1 star"></label>
              </fieldset>
            </div>
            <div style="float:right;    display: inline-flex;">
              <div class="avatar-preview">
                <div
                  class="c2"
                  id="imagePreview"
                  v-bind:style="{'background-image': 'url('+ review.profileImg +')'}"
                  style="float:left;"
                ></div>
              </div>
              <span>{{review.nickname}}</span>
            </div>
          </div>
          <div style="clear:both;">
            <div style="float:right; margin-top:10px;">
              <p>{{review.date}}</p>
            </div>
          </div>
        </div>

        <div>
          <editor-content
            id="tiptaparea"
            class="editor__content"
            :editor="editor"
            v-html="review.content"
          />
        </div>

        <!-- 좋아요 보관  댓글허용스위치-->
        <div v-if="isLogin">
          <div style="display: inline-block; width: 100%;">
            <div v-if="!liked" style="width:35px; margin-top:30px; float:left;" @click="addHeart">
              <img src="@/assets/images/like_favorite_heart_5759.png" style="width: inherit;" />
            </div>
            <div v-if="liked" style="width:35px; margin-top:30px; float:left;" @click="delHeart">
              <img
                src="@/assets/images/heart_like_love_twitter_icon_127132.png"
                style="width: inherit;"
              />
            </div>
            <div
              v-if="!bookmarked&&(review.userIdFk!=user.userId)"
              style="width:35px; margin-top:30px; float:left; margin-left:10px;"
              @click="addBookmark"
            >
              <img src="@/assets/images/bookmark_before.png" style="width: inherit;" />
            </div>
            <div
              v-if="bookmarked&&(review.userIdFk!=user.userId)"
              style="width:35px; margin-top:30px; float:left; margin-left:10px;"
              @click="delBookmark"
            >
              <img src="@/assets/images/bookmark_after.png" style="width: inherit;" />
            </div>

            <div v-if="review.userIdFk==user.userId">
              <SwitchComponent style="float:right;" :isOn.sync="isOn" :initial="isOn" />
              <span style="float:right; margin-top: 40px;">댓글 허용</span>
            </div>
          </div>
        </div>

        <!-- 댓글 영역 -->
        <div style="margin-top:15px; display: flow-root;">
          <div v-if="review.isreply==0">
            <span v-if="!isLogin">
              <span>로그인 하러 갈까요?</span>
              <router-link style="color:blue;" v-bind:to="{name:'Login'}" class="btn--text">로그인하기</router-link>
            </span>
            <span v-if="isLogin">댓글을 허용하지 않는 게시물입니다</span>
          </div>
          <div v-if="review.isreply==1">
            <!-- user profile -->
            <div class="avatar-preview">
              <div
                class="c2"
                id="imagePreview"
                v-bind:style="{'background-image': 'url('+ this.user.profileImg +')'}"
                style="float:left;"
              ></div>
            </div>
            <!-- avatar c2 -->
            <div class="c3" style="float:left; display: flex;width: 90%;">
              <div style="clear:both; width: 100%;">
                <span style="margin-left:15px;">{{user.nickname}}</span>

                <input
                  v-model="comment"
                  style="width: 93%; margin-top: 15px;"
                  placeholder="답글달기.."
                  type="text"
                />
                <button @click="addReply" style="margin-left:10px; margin-top:15px;">게시</button>
              </div>
            </div>
            <!-- c3 -->
          </div>
          <!-- v-if -->
        </div>
      </div>
      <!-- c4 -->
      <hr />
      <!-- 댓글 리스트 -->

      <div class="reply-area c4" style="display: flow-root;">
        <div v-for="(com,index) in comments" v-bind:key="index" style="clear: both;">
          <div class="avatar-preview">
            <div
              class="c2"
              id="imagePreview"
              v-bind:style="{'background-image': 'url('+ com.profileImg +')'}"
              style="float:left;"
            ></div>
          </div>
          <!-- avatar-preview c2 -->
          <div class="c3" style="float:left;width: 90%;">
            <p style="position: relative;margin-left: 15px;">
              {{com.nickname}}
              <br />
              {{com.comment}}
              <span v-if="(user.userId==review.userIdFk)||(user.userId==com.userId)">
                <img
                  src="@/assets/images/deleteReplyIcon.png"
                  width="20px"
                  height="20px"
                  style="float: right;"
                  @click="deleteReply(index)"
                />
              </span>
            </p>
          </div>
          <!-- comment c3 -->
        </div>
      </div>
      <!-- for -->
    </div>
    <!-- containerB -->
  </div>
</template>

<script>
import "@/assets/css/layout.scss";
import $ from "jquery";
import ReviewApi from "../../apis/ReviewApi";
import UserApi from "../../apis/UserApi";
import NotiApi from "../../apis/NotiApi";

import SwitchComponent from "@/components/common/Switch.vue";
import Navbar from "../../components/common/Navbarsub.vue";

import { Editor, EditorContent, EditorMenuBar } from "tiptap";
import {
  Blockquote,
  CodeBlock,
  HardBreak,
  Heading,
  HorizontalRule,
  OrderedList,
  BulletList,
  ListItem,
  TodoItem,
  TodoList,
  Bold,
  Code,
  Italic,
  Link,
  Strike,
  Underline,
  History
} from "tiptap-extensions";

export default {
  components: {
    EditorContent,
    SwitchComponent,
    Navbar
  },

  props: {
    reviewId: {
      type: String,
      required: true
    }
  },
  data: () => {
    return {
      review: {},
      liked: false,
      bookmarked: false,
      editor: new Editor(),
      user: {},
      comment: "",
      comments: [],
      isOn: '',
      isLogin: false
    };
  },
  mounted() {
    var reviewId = this.reviewId;

    ReviewApi.getReviewResult(
      reviewId,
      res => {
        this.review = res.data;
        this.isOn = this.review.isreply;
        console.log(this.review);
      },
      error => {
        console.log(error);
      }
    );

    let data = {
      reviewId: this.reviewId,
      userId: localStorage.getItem("userId")
    };
    ReviewApi.checkHeart(
      data,
      res => {
        this.liked = res.data;
      },
      error => {
        console.log(error);
      }
    );

    ReviewApi.checkBookmark(
      data,
      res => {
        this.bookmarked = res.data;
      },
      error => {
        console.log(error);
      }
    );

    if (localStorage.getItem("userId") != null) {
      this.isLogin = localStorage.getItem("userId");
      UserApi.getUserbyUserId(
        this.isLogin,
        res => {
          this.user = res.data;
          this.isLogin = true;
        },
        error => {
          console.log(error);
        }
      );
    }

    ReviewApi.getAllReply(
      this.reviewId,
      res => {
        for (var i = 0; i < res.data.length; i++) {
          this.comments.push(res.data[i]);
        }
      },
      error => {
        console.log("getAllReply Error");
      }
    );
    
    $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
  },
  watch: {
    isOn: function(v) {
      if (this.review.isreply != this.isOn) {
        this.$set(this.review, "isreply", v);
        this.$set(this.review, "reviewId", this.reviewId);
        this.updateReview(this.review); //update review
      }
    }
  },
  computed: {
    thumbnailStyle: function() {
      return {
        fontFamily: this.review.font,
        fontSize: this.review.fontSize + "px",
        fontWeight: this.review.fontColor,
        backgroundImage:
          "url(http://localhost:8080/static/" + this.review.backgroundImg + ")"
      };
    }
  },
  methods: {
    addHeart() {
      let data = {
        reviewId: this.reviewId,
        userId: localStorage.getItem("userId")
      };
      ReviewApi.addHeart(
        data,
        res => {
          this.liked = true;
        },
        error => {
          console.log(error);
        }
      );
      let notification = {
        sendUserId: localStorage.getItem("userId"),
        receiveUserId: this.review.userIdFk,
        notType: "like",
        notContentId: this.reviewId,
        notMessage: this.user.nickname+"님이 좋아요를 눌렀습니다.",
        notUrl: "http://13.209.16.137:8080/index.html#/reviewdetail/"+this.reviewId

      };
      NotiApi.addNoti(
        notification,
        res => {
          console.log("success add noti");
        },
        error => {

        }
      )
    },
    delHeart() {
      let data = {
        reviewId: this.reviewId,
        userId: localStorage.getItem("userId")
      };
      ReviewApi.delHeart(
        data,
        res => {
          this.liked = false;
        },
        error => {
          console.log(error);
        }
      );
    },
    addBookmark() {
      let data = {
        reviewId: this.reviewId,
        userId: localStorage.getItem("userId")
      };
      ReviewApi.addBookmark(
        data,
        res => {
          this.bookmarked = true;
        },
        error => {
          console.log(error);
        }
      );
    },
    delBookmark() {
      let data = {
        reviewId: this.reviewId,
        userId: localStorage.getItem("userId")
      };
      ReviewApi.delBookmark(
        data,
        res => {
          this.bookmarked = false;
        },
        error => {
          console.log(error);
        }
      );
    },
    addReply() {
      let data = {
        replyId: 0,
        reviewId: this.reviewId,
        userId: this.user.userId,
        profileImg: this.user.profileImg,
        nickname: this.user.nickname,
        comment: this.comment
      };

      ReviewApi.addReply(
        data,
        res => {
          this.comments.push(res.data);
          this.comment = "";
        },
        error => {
          console.log(error);
        }
      );
    },
    updateReview(review) {
      ReviewApi.updateReview(
        review,
        res => {
          console.log("변경:" + this.isOn);
        },
        error => {
          console.log(Error);
        }
      );
    },
    deleteReply(index) {
      var replyId = this.comments[index].replyId;
      console.log("삭제할 replyId:" + replyId);
      ReviewApi.deleteReply(
        replyId,
        res => {
          this.comments.splice(index, 1);
        },
        error => {
          console.log(error);
        }
      );
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$font-size__5: 0.857em; /*12px*/
$ssafy: #3893a8;
.switch-component {
  width: 40px;
  height: 18px;
  border-radius: 30px;
  background: #999;
  float: left;
  position: relative;
  font-size: $font-size__5;
  text-align: left;
  color: rgba(255, 255, 255, 0.5);
  padding: 0 8px;
  margin-bottom: 50px;
  top: 44px;

  &:before {
    content: "";
    position: absolute;
    width: 26px;
    height: 26px;
    border-radius: 26px;
    background: #eee;
    left: -2px;
    top: -4px;
    box-sizing: border-box;
    transition: 0.2s;
  }

  &.active {
    background: rgba(56, 147, 168, 0.5);
    color: #fff;
    text-align: right;

    &:before {
      background: $ssafy;
      left: 15px;
    }
  }
}

.avatar-preview {
  width: 3em;
  height: 3em;
  float: left;
  position: relative;
  border-radius: 100%;
  border: 6px solid #f8f8f8;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  > div {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
}
input:hover {
  border: none;
}
input:focus {
  border-bottom: 1px solid #3893a8;
}

@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
fieldset,
label {
  margin: 0;
  padding: 0;
}
.rating {
  border: none;
  float: left;
}

.rating > input {
  display: none;
}
.rating > label:before {
  margin: 5px;
  font-size: 1.25em;
  font-family: FontAwesome;
  display: inline-block;
  content: "\f005";
}

.rating > label {
  color: #ddd;
  float: right;
}

/***** CSS Magic to Highlight Stars on Hover *****/

.rating > input:checked ~ label /* show gold star when clicked */
 {
  color: #ffd700;
} /* hover previous stars in list */
</style>