<template>
  <div>
    <navbar></navbar>
    <div class="containerB" style="overflow-y:hidden;">
      <div class="c4">
        <div class="relay-search-wrap" style="display: flow-root;">
          <button class="back" @click="niceback">
            <i class="fas fa-chevron-left"></i>
          </button>
          <div class="wrap" style="float:right;">
            <div class="search">
              <input
                type="text"
                v-model="keyword"
                @keyup.enter="SearchRelay(completelist)"
                style="height:auto;"
                class="searchTerm"
                placeholder="검색.."
              />
              <button type="submit" @click="SearchRelay(completelist)" class="searchButton">
                <i class="fa fa-search"></i>
              </button>
            </div>
          </div>
        </div>
        <hr />

        <div class="tabbed round">
          <ul>
            <!-- <li>Third tab</li> -->
            <li @click="completelist=true" v-bind:class="{active:completelist}">완료</li>
            <li @click="completelist=false" v-bind:class="{active:!completelist}">작성중</li>
          </ul>
        </div>

        <!-- 진행중인 소설 -->
        <div v-if="!completelist" class="relay-board-wrap">
          <ul class="relay-board-list" v-for="(relay,index) in relaysIng" v-bind:key="index">
            <span class="post-link-wrap" style="display: inline-flex;">
              <div class="post-thumbnail-img">
                <img src="@/assets/images/bookcover.png" style="width:50px;" />
              </div>
              <div @click="goToDetail(relay)" class="post-information">
                <li class="title">
                  <span style="font-size:1.5em;">{{relay.title}}</span>
                </li>
                <li class="date">
                  <div style="margin-left: 15px; margin-right:5px;display: inline-flex;">
                    <span style="margin:0; margin-right: auto;">수정: {{relay.updateDate}}</span>
                    <div v-if="relay.editable" style="width:4%;float:right; margin-right:10px;">
                      <img src="@/assets/images/writingpossible.png" style="width:100%;" />
                    </div>

                    <!-- <span v-if="relay.isPublic">{{relay.heartcnt}}</span> -->
                    <div style="width:4%;float:right;">
                      <img
                        src="@/assets/images/heart_like_love_twitter_icon_127132.png"
                        style="width:100%;"
                      />
                    </div>
                    <span style="margin:0; margin-left:5px; color:#e16a70">{{relay.heartcnt}}</span>
                  </div>
                </li>
                <li class="like">
                  <div style="width:100%; display:inline-flex;"></div>
                </li>
              </div>
            </span>
          </ul>
        </div>

        <!-- 완료된 소설 -->
        <div v-if="completelist" class="relay-board-wrap">
          <ul class="relay-board-list" v-for="(relay,index) in relaysCom" v-bind:key="index">
            <span class="post-link-wrap" style="display: inline-flex;">
              <div class="post-thumbnail-img">
                <img src="@/assets/images/bookcover.png" style="width:50px;" />
              </div>
              <div @click="goToDetail(relay)" class="post-information">
                <li class="title">
                  <span style="font-size:1.5em;">{{relay.title}}</span>
                </li>
                <li class="date">
                  <div style="margin-left: 15px; margin-right:5px;display: inline-flex;">
                    <span style="margin:0; margin-right: auto;">수정: {{relay.updateDate}}</span>
                    <div v-if="relay.editable" style="width:4%;float:right; margin-right:10px;">
                      <img src="@/assets/images/writingpossible.png" style="width:100%;" />
                    </div>

                    <!-- <span v-if="relay.isPublic">{{relay.heartcnt}}</span> -->
                    <div style="width:4%;float:right;">
                      <img
                        src="@/assets/images/heart_like_love_twitter_icon_127132.png"
                        style="width:100%;"
                      />
                    </div>
                    <span style="margin:0; margin-left:5px; color:#e16a70">{{relay.heartcnt}}</span>
                  </div>
                </li>
                <li class="like">
                  <div style="width:100%; display:inline-flex;"></div>
                </li>
              </div>
            </span>
          </ul>
        </div>

        <!-- 작성버튼 -->

        <button
          style="float:right; margin-top:50px;"
          type="button"
          class="btn btn-sm btn-outline-dark"
          v-if="userId!=null"
          @click="goToRelayCreate"
        >릴레이소설시작!</button>
      </div>
    </div>
  </div>
</template>

<script>
import RelayApi from "../../apis/RelayApi";
import Navbar from "../../components/common/Navbarsub.vue";
import $ from 'jquery';

export default {
  components: {
    Navbar
  },
  data() {
    return {
      relaysIng: [],
      relaysCom: [],
      completelist: false,
      keyword: "",
      userId: ""
    };
  },
  mounted() {
    this.userId = localStorage.getItem("userId");
    this.SearchRelay(true); //get all ing
    this.SearchRelay(false); //get all com

            

        $(window).ready(function() {
        $(window).scroll(function() {
            if ($(document).scrollTop() > 50) {
            $('.nav').addClass('affix');
            console.log("OK");
            } else {
            $('.nav').removeClass('affix');
            }
        });

        $(".navTrigger").click(function () {
            $(this).toggleClass('active');
            console.log("Clicked menu");
            $("#mainListDiv").toggleClass("show_list");
            $("#mainListDiv").fadeIn();
            });
        })
  },
  methods: {
    getHeartcnt(relaynovelId, index, sel) {
      RelayApi.getHeartcnt(
        relaynovelId,
        res => {
          if (sel == 1) {
            this.$set(this.relaysIng[index], "heartcnt", res.data);
          } else if (sel == 0) {
            this.$set(this.relaysCom[index], "heartcnt", res.data);
          }
        },
        error => {
          console.log("getHeartcnt Error");
        }
      );
    },
    checkEditable(relaynovelId, index) {
      let data = {
        relaynovelId: relaynovelId,
        userId: localStorage.getItem("userId")
      };
      RelayApi.checkEditable(
        data,
        res => {
          this.$set(this.relaysIng[index], "editable", res.data);
        },
        error => {
          console.log("checkEditable Error");
        }
      );
    },

    SearchRelay(completelist) {
      let sel = completelist == false ? 1 : 0;
      let data = {
        sel: sel, //1:ing, 0:com
        keyword: this.keyword
      };

      RelayApi.SearchRelay(
        data,
        res => {
          if (sel == 1) {
            //sel 1:ing, 0:complete

            let len = this.relaysIng.lengh;
            this.relaysIng = this.relaysIng.splice(0, len);
            for (let i = 0; i < res.data.length; i++) {
              this.relaysIng.push(res.data[i]);
              this.getHeartcnt(res.data[i].relaynovelId, i, 1);

              if(this.userId == null) {
                this.$set(this.relaysIng[i], "editable", false);
                continue;
              }

              if (res.data[i].isPublic == 0) {
                this.checkEditable(res.data[i].relaynovelId, i);
              } else {
                this.$set(this.relaysIng[i], "editable", true);
              }
            }
          } else {
            //alert("Dd");
            let len = this.relaysCom.length;
            this.relaysCom = this.relaysCom.splice(0, len);

            for (let i = 0; i < res.data.length; i++) {
              this.relaysCom.push(res.data[i]);
              this.getHeartcnt(res.data[i].relaynovelId, i, 0); //sel 1:ing, 0:complete
              this.$set(this.relaysCom[i], "editable", false);
            }
          }
        },
        error => {
          console.log("SearchRelay Error");
        }
      );
    },

    goToDetail(relay) {
      this.$router.push({
        name: "RelayDetail",
        params: { relay: relay }
      });
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    },
    goToRelayCreate() {
      this.$router.push("/relaycreate/");
    }
  }
};
</script>

<style lang="scss" scoped>
.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #00b4cc;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 40px;
  //height: 36px;
  height: auto;
  border: 1px solid #00b4cc;
  background: #00b4cc;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 30%;
}

/* Tabbed Styles */
.tabbed {
  width: 100%;
  min-width: 400px;
  margin: 0 auto;
  margin-bottom: 68px;
  border-bottom: 4px solid #000;
  overflow: hidden;
  transition: border 250ms ease;
}
.tabbed ul {
  margin: 0px;
  padding: 0px;
  overflow: hidden;
  float: left;
  padding-left: 48px;
  list-style-type: none;
}
.tabbed ul * {
  margin: 0px;
  padding: 0px;
}
.tabbed ul li {
  display: block;
  float: right;
  padding: 10px 24px 8px;
  background-color: #fff;
  margin-right: 46px;
  z-index: 2;
  position: relative;
  cursor: pointer;
  color: #777;

  text-transform: uppercase;
  //font: 600 13px/20px roboto, "Open Sans", Helvetica, sans-serif;

  transition: all 250ms ease;
}
.tabbed ul li:before,
.tabbed ul li:after {
  display: block;
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 44px;
  background-color: #fff;
  transition: all 250ms ease;
}
.tabbed ul li:before {
  right: -24px;
  transform: skew(30deg, 0deg);
  box-shadow: rgba(0, 0, 0, 0.1) 3px 2px 5px,
    inset rgba(255, 255, 255, 0.09) -1px 0;
}
.tabbed ul li:after {
  left: -24px;
  transform: skew(-30deg, 0deg);
  box-shadow: rgba(0, 0, 0, 0.1) -3px 2px 5px,
    inset rgba(255, 255, 255, 0.09) 1px 0;
}
.tabbed ul li:hover,
.tabbed ul li:hover:before,
.tabbed ul li:hover:after {
  background-color: #f4f7f9;
  color: #444;
}
.tabbed ul li.active {
  z-index: 3;
}
.tabbed ul li.active,
.tabbed ul li.active:before,
.tabbed ul li.active:after {
  background-color: #000;
  color: #fff;
}

/* Round Tabs */
.tabbed.round ul li {
  border-radius: 8px 8px 0 0;
}
.tabbed.round ul li:before {
  border-radius: 0 8px 0 0;
}
.tabbed.round ul li:after {
  border-radius: 8px 0 0 0;
}
</style>