/*
 User API 예시
 */

import http from "../http-common";
const requestLogin = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .post("/account/login/", data)
        .then(Response => {

            console.log("login");
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })


}
const requestKakaoLogin = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .post("/account/kakaologin", data)
        .then(Response => {

            console.log("login");
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })


}

const requestKakaoLogout = (data, callback, errorCallback) => {
    //백앤드와 로그인 통신하는 부분
    http
        .get("/account/kakaologin?token=" + data)
        .then(Response => {

            console.log("login");
            console.log(Response);
            callback(Response);
        })
        .catch(Error => {
            console.log("login error :" + Error);
            errorCallback(Error);
        })


}
const requestJoin = (data, callback, errorCallback) => {
    http
        .post("/account/signup", data)
        .then(Response => {
            console.log("join success: " + Response);
            callback(Response);
        }).catch(Error => {
            console.log("join error :" + Error);
            errorCallback(Error);
        })
}

const requestEmailCheck = (data, callback, errorCallback) => {
    http
        .get("/account/emailCheck2?email=" + data["email"] + "&nickname=" + data["nickname"])
        .then(Response => {
            console.log("join success: ");
            console.log(Response);
            callback(Response);
        }).catch(Error => {
            console.log("join error :" + Error);
            errorCallback(Error);
        })
}

const deleteUser = (data, callback, errorCallback) => {
    http
        .delete("/account/quit?email=" + data["originEmail"])
        .then(Response => {
            console.log("join success: " + Response);
            callback(Response);
        }).catch(Error => {
            console.log("join error :" + Error);
            errorCallback(Error);
        })
}

