const updateUser = (data, callback, errorCallback) => {
    http
        .post("/account/update", data)
        .then(Response => {
            console.log(Response);
            callback(Response);
        }).catch(Error => {
            console.log(Error);
            errorCallback(Error);
        })
}

const UserApi = {
    requestLogin: (data, callback, errorCallback) => requestLogin(data, callback, errorCallback),
    requestKakaoLogin: (data, callback, errorCallback) => requestKakaoLogin(data, callback, errorCallback),
    requestKakaoLogout: (data, callback, errorCallback) => requestKakaoLogout(data, callback, errorCallback),
    requestJoin: (data, callback, errorCallback) => requestJoin(data, callback, errorCallback),
    requestEmailCheck: (data, callback, errorCallback) => requestEmailCheck(data, callback, errorCallback),
    deleteUser: (data, callback, errorCallback) => deleteUser(data, callback, errorCallback),
    getUserbyUserId: (data, callback, errorCallback) => getUserbyUserId(data, callback, errorCallback),
    requestgetUser: (data, callback, errorCallback) => requestgetUser(data, callback, errorCallback),
    updateUser: (data, callback, errorCallback) => updateUser(data, callback, errorCallback),
}

export default UserApi