/* 
    @Author 전수환
    @Category Profile.vue
    @data 2020.02.14

    Done:
    유저 프로필 Layout 재설정
    페이지 전체 media screen 적용
    유저 DB 데이터 정보 가져오기
    nickname, status 프론트에 적용
    타 유저 페이지일 때 Edit profile을 Follow 버튼으로 바꾸기
    + 설정 버튼 없애기
    본인 페이지일 때 followers following, post, nickname 뿌리기
    review 데이터 하나도 없을 시 아직 등록된 데이터 없다고 띄우기
    review 개수만큼 count해서 posts에 뿌리기
    follow, unfollow 기능 구현하기
    follow, unfollow 클릭 시 각각의 user Information 노출
    follow, unfollow시 DB에 적용하고 count해서 바인딩하기
    북마크 읽어올때 이야기 counting 현상
    북마크 tab 눌렀을때 객체는 가져와지는데 안뜨는거
    타유저 bookmark는 볼 수 없고 나의 bookmark만 볼 수 있게하기 

    To do:
    부족한 부분이 있는지 팀원이랑 상의해보고 보충하기
 */
<template>
  <div>
    <navbar></navbar>
    <div style="margin: 4vh 9vh 0vh 9vh;">
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
      </div>
    <div class="container_q">
      
      <div class="profile">
        
        <div class="profile-image" v-bind="users">
          
          <img :src="users.profileImg" alt />
        </div>

        <div class="profile-user-settings">
          <h1 class="profile-user-name">{{users.nickname}}</h1>

          <button @click="editProfile" class="btn profile-edit-btn" v-show="yourself">프로필 수정</button>
          <button
            class="btn profile-edit-btn"
            v-show="!followingFlag&&!yourself"
            @click="clickFollow()"
          >읽기</button>
          <button
            class="btn profile-edit-btn"
            v-show="followingFlag&&!yourself"
            @click="clickUnFollow()"
          >읽지 않기</button>
        </div>

        <div class="profile-stats">
          <ul>
            <li>
              <span class="profile-stat-count">{{countPost}}</span> 이야기
            </li>
            <li v-on:click="followers3()" v-b-modal.modal-scrollable>
              <b-modal id="modal-scrollable" scrollable title="읽는 이" style="text-align:center">
                <ul style="margin: 0 30px;">
                  <li class="my-1" v-for="(user, i) in followers" :key="i">
                    <div class="row" style="margin-bottom: 10px;">
                      <div
                        class="col-1"
                        style="padding: 0px; vertical-align: middle; text-align: center;"
                      >
                        <img id="imgInfollow" :src="user.profileImg" />
                      </div>
                      <div class="col-9" style="padding: 0px 30px;">
                        <p style=" margin-bottom: 0px;">{{user.nickname}}</p>
                        <p>{{user.status}}</p>
                      </div>
                      <div class="col-2" style="padding: 0px;">
                        <button v-on:click="goToProfile(user.userId)" class="follow-btn">
                          <a class="btn-follow-btn">상세</a>
                        </button>
                      </div>
                    </div>
                  </li>
                </ul>
              </b-modal>
              <span class="profile-stat-count">{{countFollowers}}</span> 읽는 이
            </li>

            <li v-on:click="followings()" v-b-modal.modal-scrollable1>
              <b-modal
                id="modal-scrollable1"
                scrollable
                title="읽는 중인 사용자"
                style="text-align:center"
              >
                <ul style="margin: 0 30px;">
                  <li class="my-1" v-for="(user, i) in following" :key="i">
                    <div class="row" style="margin-bottom: 10px;">
                      <div
                        class="col-1"
                        style="padding: 0px; vertical-align: middle; text-align: center;"
                      >
                        <img id="imgInfollow" :src="user.profileImg" />
                      </div>
                      <div class="col-9" style="padding: 0px 30px;">
                        <p style=" margin-bottom: 0px;">{{user.nickname}}</p>
                        <p>{{user.status}}</p>
                      </div>
                      <div class="col-2" style="padding: 0px;">
                        <button v-on:click="goToProfile1(user.userId)" class="follow-btn">
                          <a class="btn-follow-btn">상세</a>
                        </button>
                      </div>
                    </div>
                  </li>
                </ul>
              </b-modal>
              <span class="profile-stat-count">{{countFollowings}}</span> 읽는 중
            </li>
          </ul>
        </div>

        <div class="profile-bio">
          <p>
            <span class="profile-real-name">소개</span>
            {{users.status}}
          </p>
        </div>
      </div>
      <!-- End of profile section -->
    </div>
    <!-- Starting tab-->
    <tabs
      :tabs="tabs"
      :currentTab="currentTab"
      :wrapper-class="'default-tabs'"
      :tab-class="'default-tabs__item'"
      :tab-active-class="'default-tabs__item_active'"
      :line-class="'default-tabs__active-line'"
      @onClick="handleClick"
    />
    <div class="content">
      <!-- TAB1 -->
      <div v-if="currentTab === 'tab1'">
        <div v-if="isReview">
          <ul id="rig">
            <li v-for="(post,index) in posts" :key="index">
              <a class="rig-cell" v-on:click="goToReview(post.reviewId)">
                <img class="rig-img" :src="post.img" />
                <span class="rig-overlay">{{post.date}}</span>
                <span
                  class="rig-text"
                  style="margin:0;"
                  :style="{fontFamily: post.font, fontSize: post.fontSize, fontWeight: post.fontColor}"
                  v-html="post.titleText"
                ></span>
              </a>
            </li>
          </ul>
        </div>
        <div v-else style="text-align:center">
          <br />
          <br />
          <p>아직 등록된 포스팅이 없습니다.</p>
          <a
            href="http://13.209.16.137:8080/index.html#/reviewcreate"
            v-if="yourself"
          >{{users.nickname}}님의 리뷰를 스케치 하러가기!</a>
        </div>
      </div>
      <!-- TAB2 -->
      <div v-if="currentTab === 'tab2'">
        <div class="tab_2">
          <div v-if="isbookmark && yourself">
            <ul id="rig">
              <li v-for="(bookmark,index) in bookmarks" :key="index">
                <a class="rig-cell" v-on:click="goToReview(bookmark.reviewId)">
                  <img class="rig-img" :src="bookmark.mainImg" />
                  <span class="rig-overlay">{{bookmark.date}}</span>
                  <span
                    class="rig-text"
                    style="margin:0;"
                    :style="{fontFamily: bookmark.font, fontSize: bookmark.fontSize, fontWeight: bookmark.fontColor}"
                  >{{bookmark.titleText}}</span>
                </a>
              </li>
            </ul>
          </div>
          <div v-if="!isbookmark && yourself" style="text-align:center">
            <br />
            <br />
            <p>아직 등록된 북마크가 없습니다.</p>
            <p>마음에 드는 문구를 마킹해주세요 :-)</p>
          </div>
          <div v-if="!yourself" style="text-align:center">
            <br />
            <br />
            <p>다른 사용자의 북마크는 볼 수 없습니다. >_ㅋ</p>
          </div>
        </div>
      </div>
      <!-- TAB3 -->
      <div v-if="currentTab === 'tab3'">
        <div class="tab_2" style="text-align:center;">
          <div class="relay-board-wrap" >
          <ul class="relay-board-list" v-for="(relay,index) in relays" v-bind:key="index">
            <span class="post-link-wrap" style="display: inline-flex;">
              <div class="post-thumbnail-img">
                <img src="@/assets/images/bookcover.png" style="width:50px;" />
              </div>
              <div @click="goToDetail(relay)" class="post-information">
                <li class="title" style="text-align:left;">
                  <span style="font-size:1.5em;">{{relay.title}}</span>
                </li>
                <li class="date">
                  <div style="margin-left: 15px; margin-right:5px;display: inline-flex;">
                    <span style="margin:0; margin-right: auto;">수정: {{relay.updateDate}}</span>
                    <div v-if="relay.editable" style="width:4%;float:right; margin-right:10px;">
                      <img src="@/assets/images/writingpossible.png" style="width:100%;" />
                    </div>

                    <!-- <span v-if="relay.isPublic">{{relay.heartcnt}}</span> -->
                    <div style="width:4%;float:right;">
                      <img
                        src="@/assets/images/heart_like_love_twitter_icon_127132.png"
                        style="width:100%;"
                      />
                    </div>
                    <span style="margin:0; margin-left:5px; color:#e16a70">{{relay.heartcnt}}</span>
                  </div>
                </li>
                <li class="like">
                  <div style="width:100%; display:inline-flex;"></div>
                </li>
              </div>
            </span>
          </ul>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script src="js/scripts.js"></script>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.1.1/jquery.min.js"></script>
<script>
import ReviewApi from "../../apis/ReviewApi";
import UserApi from "../../apis/UserApi";
import FollowApi from "../../apis/FollowApi";
import BookmarkApi from "../../apis/BookmarkApi";
import NotiApi from "../../apis/NotiApi";
import Navbar from "../../components/common/Navbarsub.vue";
import $ from "jquery";
import Tabs from "vue-tabs-with-active-line";
import RelayApi from '../../apis/RelayApi';
const TABS = [
  {
    title:
      "&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp리뷰&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp",
    value: "tab1"
  },
  {
    title:
      "|&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp북마크&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp|",
    value: "tab2"
  },
  {
    title:
      "&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp릴레이 소설&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp",
    value: "tab3"
  }
];
export default {
  components: {
    Navbar,
    Tabs
  },
  props: {
    userId: {
      type: String,
      required: true
    }
  },

  data() {
    return {
      tabs: TABS,
      currentTab: "tab1",
      users: [],
      posts: [],
      relays: [],
      bookmarks: [],
      following: [],
      followers: [],
      yourself: true,
      isReview: true,
      isbookmark: true,
      isFollow: false,
      countPost: 0,
      countFollowers: 0,
      countFollowings: 0,

      followingFlag: false
    };
  },
  mounted() {
    this.getMyRelays();
    this.getMyReviews();
    this.getUserInfo();
    this.followers2();
    this.followings();
    this.bookmark();
    
    $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
  },
  created() {
    if (this.userId != localStorage.getItem("userId")) {
      this.yourself = !this.yourself;
    }
  },
  methods: {
    async bookmark() {
      console.log("<------- BOOKMARK -------->");
      let userId = localStorage.getItem("userId");
      BookmarkApi.getBookmarkByUser(
        userId,
        async res => {
          if (res == null) {
            console.log("null");
          }
          this.bookmarks = res.data;
          if (this.bookmarks.length == 0) {
            this.isbookmark = !this.isbookmark;
          }
        },
        error => {}
      );
    },

    handleClick(newTab) {
      this.currentTab = newTab;
    },
    goToProfile1(userId) {
      alert("HIHI");
      // this.$router.push("/user/profile/" + userId);
      this.$router.go();
      this.$router.push({
        name: "Profile",
        params: { userId: userId }
      });
      this.$router.reload();
    },
    clickFollow() {
      this.isFollow = true;
      this.follow();
    },
    clickUnFollow() {
      this.isFollow = false;
      this.unfollow();
    },
    MainInit() {
      $(window).scroll(function() {
        if ($(document).scrollTop() > 50) {
          $(".nav").addClass("affix");
          console.log("OK");
        } else {
          $(".nav").removeClass("affix");
        }
      });

      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        console.log("Clicked menu");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },
    getUserInfo() {
      console.log("<------- getUserInfo -------->");
      let userId = this.userId;
      let data = userId;
      UserApi.requestgetUser(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          this.users = res.data;
        },
        error => {}
      );
    },

    getMyReviews() {
      console.log("<------- getMyReviews -------->");
      let userId = this.userId;
      let data = {
        userId
      };
      ReviewApi.getReviewsByUser(
        userId,
        res => {
          //통신을 통해 전달받은 값 콘솔에 출력
          console.log(res);
          if (res == null) {
            //모름
          }
          this.posts = res.data;
          this.countPost = this.posts.length;
          if (this.countPost == 0) {
            this.isReview = !this.isReview;
          }
        },
        error => {
          console.log("에러");
          if (this.mainImg == null) {
            this.isReview = !this.isReview;
          }
        }
      );
    },
    follow() {
      console.log("<------- FOLLOW -------->");
      let userOne = localStorage.getItem("userId");
      if (userOne == null) {
        alert("로그인하여 주세요 :-)");
      }
      let userTwo = this.userId;
      let date = "2020-02-11 00:00:00";

      let data = {
        userOne: userOne,
        userTwo: userTwo,
        date: date
      };
      FollowApi.addFollow(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          console.log(res);
          this.followingFlag = true;
          this.countFollowers++;
        },
        error => {}
      );
      let notification = {
        sendUserId: localStorage.getItem("userId"),
        receiveUserId: this.userId,
        notType: "follow",
        notContentId: this.userId,
        notMessage: localStorage.getItem("nickname")+"님이 읽기를 눌렀습니다.",
        notUrl: "http://13.209.16.137:8080/index.html#/user/profile/"+localStorage.getItem("userId")

      };
      NotiApi.addNoti(
        notification,
        res => {
          console.log("success add noti");
        },
        error => {

        }
      )
    },
    unfollow() {
      console.log("<------- UNFOLLOW -------->");
      let userOne = localStorage.getItem("userId");
      let userTwo = this.userId;
      let date = "2020-02-11 00:00:00";

      let data = {
        userOne: userOne,
        userTwo: userTwo,
        date: date
      };
      FollowApi.deleteFollow(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          this.followingFlag = false;
          this.countFollowers--;
        },
        error => {}
      );
      this.followers2();
    },
    followers2() {
      console.log("<------- FOLLOWER2 -------->");
      let userId = this.userId;
      let data = {
        userId
      };
      FollowApi.getAllFollower(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          this.followers = res["data"];
          this.countFollowers = this.followers.length;
          for (let i = 0; i < this.followers.length; i++) {
            if (this.followers[i].userId == localStorage.getItem("userId")) {
              this.followingFlag = !this.followingFlag;
            }
          }
        },
        error => {}
      );
    },
    followers3() {
      console.log("<------- FOLLOWER3 -------->");
      let userId = this.userId;
      let data = {
        userId
      };
      FollowApi.getAllFollower(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          this.followers = res["data"];
          this.countFollowers = this.followers.length;
        },
        error => {}
      );
    },
    followings() {
      console.log("<------- FOLLOWINGS -------->");
      let userId = this.userId;
      let user = localStorage.getItem("userId");
      let data = { userId };
      FollowApi.getAllFollowing(
        data,
        res => {
          if (res == null) {
            console.log("res == null"); //
          }
          this.following = res["data"];
          this.countFollowings = this.following.length;
        },
        error => {}
      );
    },
    goToReview(message) {
      this.$router.push({
        name: "ReviewDetail",
        params: { reviewId: message }
      });
    },
    editProfile() {
      this.$router.push({
        name: "UserUpdate",
        params: { user: this.users }
      });
    },
    goToDetail(relay) {
      this.$router.push({
        name: "RelayDetail",
        params: { relay: relay }
      });
    },
    getMyRelays(){
      let userId = this.userId;
      RelayApi.getRelaysByUserId(userId, res => {
        this.relays = res.data;
        console.log("!!!");
        console.log(res);
        console.log(this.relays);
      },error => {
        console.log("Error");
      });
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    },
  }
};
</script>
<style scoped>
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "NotoSerifKR";
}

.default-tabs {
  text-align: center;
  position: relative;
  height: 50px;
}
.default-tabs__item {
  display: inline-block;
  padding: 10px;
  padding-bottom: 8px;
  font-size: 16px;
  letter-spacing: 0.8px;
  color: gray;
  text-decoration: none;
  border: none;
  background-color: red;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: all 0.25s;
}
.content {
  margin-top: 30px;
  font-size: 20px;
}
.tab_3 {
  max-width: 1000px;
  margin-top: 5px;
  margin: 20px auto;
}
.tab_2 {
  max-width: 1000px;
  margin-top: 5px;
  margin: 20px auto;
}
.tabs {
  text-align: center;
  position: relative;
  font-size: 20px;
}

.tabs__item {
  display: inline-block;
  margin: 0 5px;
  padding: 10px;
  padding-bottom: 8px;
  font-size: 16px;
  letter-spacing: 0.8px;
  color: gray;
  text-decoration: none;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: all 0.25s;
}

.tabs__item_active {
  color: black;
}

.tabs__item:hover {
  border-bottom: 2px solid gray;
  color: black;
}

.tabs__item:focus {
  outline: none;
  border-bottom: 2px solid gray;
  color: black;
}

.tabs__item:first-child {
  margin-left: 0;
}

.tabs__item:last-child {
  margin-right: 0;
}
.tabs__active-line {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  background-color: black;
  transition: transform 0.4s ease, width 0.4s ease;
}

/* 모달안의 follow 버튼 */
.follow-btn {
  font-size: 1rem;
  line-height: 1.8;
  border: 0.1rem solid white;
  border-radius: 0.3rem;
  padding: 0 1rem;
  background-color: #87ceeb;
  color: white;
}
.btn-follow-btn:hover {
  color: black;
}

#imgInfollow {
  max-width: 40px;
  max-height: 40px;
}
#rig {
  max-width: 1000px;
  margin: 20px auto; /*center aligned*/
  padding: 0;
  font-size: 0; /* Remember to change it back to normal font size if have captions */
  list-style: none;
}

#rig li {
  display: inline-block;
  *display: inline; /*for IE6 - IE7*/
  width: 75%;
  vertical-align: middle;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* The wrapper for each item */
.rig-cell {
  margin: 10px 9px 10px 9px;
  /*box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);*/
  display: block;
  position: relative;
  overflow: hidden;
}

/* If have the image layer */
.rig-img {
  display: block;
  width: 100%;
  height: auto;
  border: none;
  transform: scale(1);
  transition: all 1s;
}

#rig li:hover .rig-img {
  transform: scale(1.05);
}

/* If have the overlay layer */
.rig-overlay {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  background: #003569 no-repeat center 20%;
  background-size: 550px 550px;
  opacity: 0;
  filter: alpha(opacity=0); /*For IE6 - IE8*/
  transition: all 0.6s;
}
#rig li:hover .rig-overlay {
  opacity: 0.8;
}

/* If have captions */
.rig-text {
  display: block;
  padding: 0 30px;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
  text-transform: capitalize;
  font-size: 18px;
  font-weight: bold;
  font-family: "Oswald", sans-serif;
  font-weight: normal !important;
  top: 40%;
  color: white;
  opacity: 0;
  filter: alpha(opacity=0); /*For older IE*/
  transform: translateY(-20px);
  transition: all 0.3s;
}
#rig li:hover .rig-text {
  transform: translateY(0px);
  opacity: 0.9;
}

@media (max-width: 9000px) {
  #rig li {
    width: 25%;
  }
}

@media (max-width: 700px) {
  #rig li {
    width: 33.33%;
  }
}

@media (max-width: 550px) {
  #rig li {
   /*  width: 70%; */
  }
}

/* Base Styles */
:root {
  font-size: 10px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", Arial, sans-serif;
  min-height: 100vh;
  background-color: #fafafa;
  color: #262626;
  padding-bottom: 3rem;
