<template>
  <div class="bg-img">
    <navbar></navbar>
    <div class="containerC" style="margin-top: 40px;">
      <div>
        <button class="back" @click="niceback">
          <i class="fas fa-chevron-left"></i>
        </button>
      </div>
      <h1
        style="text-align: center; font-family:'MapoGoldenPier'; font-weight: bold; font-size: 40px;"
      >{{relay.title}}</h1>

      <div class="c1">
        <h5>
          <b-badge
            pill
            v-b-popover.hover.right="'릴레이 횟수를 정해두고 횟수만큼 다른 사용자들과 함께 만들어가는 소설입니다. 공개 | 비공개 설정할 수 있으며 비공개시 원하는 사용자만 초대하여 함께 소설을 작성할 수 있습니다.'"
            title="스케치북 릴레이 소설 쓰기입니다.😊"
            href="#"
            variant="info"
          >릴레이 소설이란?</b-badge>
        </h5>

        <div>
          <img
            v-if="!relay.isPublic"
            style="height: 32px;"
            src="@/assets/images/lock.png"
            alt="lock"
          />

          <span v-if="relay.remainTurn>0&&relay.remainTurn<20">현재 턴수 : {{relay.remainTurn}}</span>
          <span v-if="relay.remainTurn>20">무제한 턴수입니다.</span>
          <span v-if="relay.remainTurn==0">릴레이소설 완료되었습니다.</span>

          
          <!-- <button
            type="button"
            style="font-size: 15px; float:right;"
            class="btn btn-sm btn-outline-dark"
            id="btnList"
            v-if="userId==relay.starter"
            @click="deleteRelay"
          >소설삭제</button> -->


          
        </div>

        <comment-components
        :visible.sync="visible"
        :relaynovelId="relay.relaynovelId"
        ></comment-components>

        <div class="relay-story-wrap" style="margin-top:50px; margin-bottom:50px;">
          <!-- Description -->
          <div
            class="relay-summary"
            dir="ltr"
            aria-disabled="false"
            spellcheck="true"
            v-html="relay.description"
            style="margin-bottom:50px"
          ></div>

          <div class="relay-story" style="margin-bottom:50px;" id="relayStory" v-html="story"></div>

          <!-- 내용 입력 -->
          <div
            v-if="isrelay"
            ref="plusArea"
            dir="ltr"
            aria-disabled="false"
            placeholder="내용을 입력해주세요"
            contenteditable="true"
            class="relay-content"
            spellcheck="true"
            @input="getInnerHtml"
            style="width:100%; background:ivory;"
          ></div>
        </div>

        <div class="buttons" style="text-align: right; margin-top :30px;">
          <img 
            v-if="!like&&userId!=null"
            style="height: 2.5em; float:left;"
            src="@/assets/images/heartbefore.svg"
            @click="addHeart"
          />
          <img 
            v-if="like&&userId!=null"
            style="height: 2.5em; float:left;"
            src="@/assets/images/heartafter.svg"
            @click="delHeart"
          />
          <img 
            style="height: 2.5em; margin-left: 1rem; float:left;"
            src="@/assets/images/speech-bubble-comment-svgrepo-com.svg"
            @click="PopupComments"
            alt="comment"
          />


          <button
            v-if="relay.editable&&isrelay==false"
            type="button"
            style="margin-right: 10px; font-size: 15px;"
            class="btn btn-sm btn-dark"
            id="btnSave"
            @click="isrelay=true"
          >이어쓰기</button>

          <button
            v-if="isrelay==true"
            type="button"
            style="margin-right: 10px; font-size: 15px;"
            class="btn btn-sm btn-dark"
            id="btnSave"
            @click="addRelayWriter"
          >완료하기</button>

          <button
            v-if="isrelay"
            type="button"
            style="font-size: 15px;"
            class="btn btn-sm btn-outline-dark"
            id="btnList"
            @click="reset"
          >닫기</button>

          <p v-if="relay.editable==false&&relay.remainTurn!=0">
              <small>함께 할까요?</small>
              <span><small>
                <button class="btn--text" style="color:hotpink;" @click="requestRelay()">작성 권한 신청하기</button>
                </small>
              </span>
            </p>

            
        </div>

        
      </div>
    </div>
  </div>
</template>

<script>
//import $ from "jquery";
import "../../assets/css/review.scss";
import RelayApi from "../../apis/RelayApi";
import UserApi from "../../apis/UserApi";
import NotiApi from "../../apis/NotiApi";

import Navbar from "../../components/common/Navbarsub.vue";
import CommentComponents from "./RelayComment.vue";

import $ from "jquery";



export default {
  props: {
    relay: {
      type: Object,
      required: true
    }
  },
  components: {
    Navbar: Navbar,
    commentComponents : CommentComponents
  },
  data() {
    return {
      contents: [],
      story: "",
      plustext: "",
      fixedlen: 0,
      refstory: "",
      relaynovel: {},
      userId: 0,
      user: {},
      isrelay: false,
      visible:false,
      like: false,
      data : {}
    };
  },
  mounted() {
    this.userId = localStorage.getItem("userId");
    this.data = {
      userId : this.userId,
      relaynovelId : this.relay.relaynovelId
    }
    this.getStoryContent();

    
    if (this.userId == this.relay.starter) {
      this.$set(this.relay, "editable", true);
    }
    //let imgstr = "http://localhost:8080/static/";


    UserApi.getUserbyUserId(
      this.userId,
      res => {
        this.user = res.data;
      },
      error => {
        console.log("getUserbyUserId Error");
      }
    );

    
    RelayApi.checkHeart(
      this.data,
      res=>{
        this.like = res.data;
      },
      error=>{
        console.log("checkHeart Error");
      }
    )
    
    $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
  },
  methods: {
    getInnerHtml: function(e) {
      //console.log(this.$refs.plusArea);
      this.plustext = e.target.innerHTML;
      this.story = this.refstory.concat(this.plustext);
    },
    addRelayWriter() {
      // relaynovelid, userid, turn, content, profileimg, nickname
      let data = {
        relaynovelId: this.relay.relaynovelId,
        userId: localStorage.getItem("userId"),
        turn: this.relay.remainTurn,
        content: this.plustext,
        profileImg: this.user.profileImg,
        nickname: this.user.nickname
      };

      data.content = data.content
        .replace(/<div>/gi, "<br>")
        .replace(/<\/div>/gi, "");
      console.log(data.content);

      console.log("addrelaywriter data");
      console.log(data);
      RelayApi.addRelayWriter(
        data,
        res => {
          alert("작성이 완료 되었습니다.");

          //this.$router.push("/relay/relayboard");
          this.relay.remainTurn = this.relay.remainTurn - 1;
          this.$refs.plusArea.innerHTML = "";
          this.isrelay = false;
          if (this.relay.remainTurn == 0) {
            this.relay.editable = false;
          }
          //this.relay.updateDate = res.data.updateDate;
          //this.$set(this.relay, 'remainTurn', this.relay)

          this.getStoryContent();
        },
        error => {
          console.log("addRelayWriter Error");
        }
      );
    },
    gotoList() {
      this.$router.push("/relay/relayboard");
    },
    deleteRelay() {
      console.log("삭제 deleteRelay");
    },
    deleteInvitation() {
      console.log("초대 삭제 deleteInvitation");
    },
    askInvitarion() {
      //console.log("초대 신청 askInvitarion");
      if (this.userId == null) {
        alert("로그인 해야 가능합니다.");
      }
    },
    getStoryContent() {
      //리뷰 글 작성 가져오기
      this.refstory = "";
      //this.$refs.plusArea.innerHTML = "";
      RelayApi.getContent(
        //this.relay.relaynovelId,
        this.relay.relaynovelId,
        res => {
          for (let i = 0; i < res.data.length; i++) {
            let tmp = res.data[i].content;
            let index = 0;
            let last = -1;

            while (index < tmp.length) {
              let isbr = tmp.substring(index, index + 4);
              if (isbr == "<br>") {
                last = 0;
                index += 4;
              }
              else{
                isbr = tmp.substring(index, index+6);
                if(isbr == "&nbsp;"){
                  last = 1;
                  index += 7;
                }
                else{
                  break;
                }
              }
              if(index > 1000) break;
            }
            if(last==1) index-=1;
            console.log("index:" + index);
            let first = tmp.substring(0, index);
            let second = tmp.substring(index);
            console.log("first:" + first + " second:" + second);

            let tag =
              "<img src='" +
              res.data[i].profileImg +
              "' title='"+res.data[i].nickname
              +"'"+
              " style='width: 2rem; height: 2rem; border-radius: 50%;'/>";
           
            this.refstory = this.refstory.concat(first + tag + second);
          }
          this.story = this.refstory;
          this.fixedlen = this.story.length;
        },
        error => {
          console.log("getContent Error");
        }
      );
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    },
    reset() {
      this.isrelay = false;
      this.plustext = "";
      this.story = this.story.substring(0, this.refstory.length);
      //console.log(this.$refs.plusArea);
    },
    PopupComments(){
      this.visible = !this.visible;
    },
    requestRelay(){
      console.log(this.relay);
      let notification = {
        sendUserId: localStorage.getItem("userId"),
        receiveUserId: this.relay.starter,
        notType: "inviterequest",
        notContentId: this.relay.relaynovelId,
        notMessage: localStorage.getItem("nickname")+"님이 '"+ this.relay.title+"'글에 초대를 신청했습니다."
        // notUrl: "http://localhost:8081/#/user/profile/"+userId

      };
      NotiApi.addNoti(
        notification,
        res => {
          console.log("success add noti");
          alert("신청이 완료되었습니다.");
        },
        error => {
  
        }
      )
    },
    addHeart(){
      RelayApi.addRelayHeart(
        this.data,
        res=>{
          this.like = true;
        },
        error=>{
          console.log("addRelayHeart Error");
        }
      );
    },
    delHeart(){
      RelayApi.delRelayHeart(
        this.data,
        res=>{
          this.like = false;
        },
        error=>{
          console.log("delRelayHeart Error");
        }
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.bg-img {
  /* border: 0;
  padding: 0; */
  background-image: url("http://www.designbolts.com/wp-content/uploads/2013/02/Free-Seamless-Wood-Textures-Patterns-For-3D-Mapping.jpg");
  /* background-repeat: no-repeat; */
  /* background: center fixed; */
  //width: 100%;
  //min-height: 100%;
  width: 100%;
  height: auto;
  background-size: cover;
  background-position: center;
}

div[contenteditable="true"],
div[contenteditable="true"] > * {
  word-break: break-all;
}

div[contenteditable="true"] div {
  word-break: break-all;
}

div[contenteditable="false"],
div[contenteditable="false"] > * {
  word-break: break-all;
}

.relay-story,
.relay-story div {
  word-break: break-all !important;
}

.relay-summary {
  width: 100%;
  height: 15vh;
  border-left: 4px solid rgba(128, 128, 128, 0.3);
  color: bl;
}

.relay-content {
  min-height: 150px;
  border: 1px solid rgba(128, 128, 128, 0.39);
  caret-color: rgb(114, 250, 121);
  border-radius: 20px;
}


</style>