/*
 Search API 예시
 */

import http from "../http-common";

const requestReviewSearch = (data, callback, errorCallback) => {
    http
        .get("/search/getReviewsbyTitle?title=" + data["searchText"]+"&indexnum="+data["reviewindexnum"])
        .then(response => {

            console.log(response);
            callback(response);
        })
        .catch(Error => {
            console.log("search error :" + Error);
            errorCallback(Error);
        })
}
const requestUserSearch = (data, callback, errorCallback) => {
    http
        .get("/search/getUsersbyNickname?title=" + data["searchText"]+"&indexnum="+data["userindexnum"])
        .then(response => {

            console.log(response);
            callback(response);
        })
        .catch(Error => {
            console.log("search error :" + Error);
            errorCallback(Error);
        })
}



const SearchApi = {
    requestReviewSearch: (data, callback, errorCallback) => requestReviewSearch(data, callback, errorCallback),
    requestUserSearch: (data, callback, errorCallback) => requestUserSearch(data, callback, errorCallback),

}

export default SearchApi