// * @author 이채은
// * @category 리뷰 생성
// * @date 2020.01.31
// 책검색 결과 모달 책 선택시 모달 닫고 자동으로 폼에 입력되도록

<template>
  <div class="modalA" v-if="visible" @click.self="handleWrapperClick">
    <div class="modal__dialog">
      <header class="modal__header">
        <span>{{title}}</span>
      </header>
      <hr />
      <div class="modal__body">
        <!-- 검색 결과 표시 -->
        <div
          v-for="item in items"
          v-bind:key="item.isbn"
          class="book-info row"
          style="background: rgba(255, 255, 255, 0.1);"
        >
          <div
            class="col-4 card-img-body"
            style="display: flex; align-items: center; justify-content: center;"
          >
            <div>
              <img
                style="margin-top:5px;"
                v-bind:src="item.image"
                alt="Card image cap"
                width="auto"
              />
            </div>
          </div>
          <div class="col-8 card-body">
            <p v-html="item.title">{{item.title}}</p>
            <p>저자 : {{item.author}}</p>
            <div style="position:absolute; display: block;">
              <button style="color: #e86b81;" v-on:click="selectedBook(item)">선택</button>
            </div>
          </div>
        </div>
      </div>
      <hr />
      <div class="text-center">
      <ul class="pagination" style="display:inline-flex;">
        <li class="page-item" :class="{ disabled : !(offset-1) }"><a @click="(offset=offset-5)" class="page-link" href="#">Previous</a></li>
        <li class="page-item" :class="{active : index==offset+0}"><a @click="ChangeIndex(offset+0)" class="page-link" href="#">{{offset+0}}</a></li>
        <li class="page-item" :class="{active : index==offset+1}"><a @click="ChangeIndex(offset+1)" class="page-link" href="#">{{offset+1}}</a></li>
        <li class="page-item" :class="{active : index==offset+2}"><a @click="ChangeIndex(offset+2)" class="page-link" href="#">{{offset+2}}</a></li>
        <li class="page-item" :class="{active : index==offset+3}"><a @click="ChangeIndex(offset+3)" class="page-link" href="#">{{offset+3}}</a></li>
        <li class="page-item" :class="{active : index==offset+4}"><a @click="ChangeIndex(offset+4)" class="page-link" href="#">{{offset+4}}</a></li>
        <li class="page-item"><a @click="(offset=offset+5)" class="page-link" href="#">Next</a></li>
      </ul>
      </div>

      <div class="modal__footer btns-small">
        <button
          @click="$emit('update:visible', !visible)"
          class="close"
          style="margin-right:20px;"
        >확인</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "bookSearchModal",

  props: {
    visible: {
      type: Boolean,
      require: true,
      default: false
    },
    title: {
      type: String,
      require: false
    },
    keyword: {
      type: String,
      required: true,
      default: ""
    },
    item: {
      type: Object,
      required: true
    },
    items: {
      type: Array,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },

  data: function() {
    return {
      offset:1
      //index: 1,
      //items: []
    };
  },
  methods: {
    handleWrapperClick() {
      this.$emit("update:visible", false);
    },
    checkForm() {
      this.$emit("update:isTerm", this.checks);
    },
    selectedBook(item) {
      //alert("selected");

      item.title = item.title
        .replace(/<b>/gi, "")
        .replace(/<\/b>/gi, "");
      //console.log(item.title);

      item.author = item.author
        .replace(/<b>/gi, "")
        .replace(/<\/b>/gi, "");
      //console.log(item.title);

      /* var idx1 = item.title.indexOf("<b>");
      var idx2 = item.title.indexOf("</b>");
      var tmp =
        item.title.substring(0, idx1) +
        this.keyword +
        item.title.substring(idx2 + 4, item.title.length); */

      this.item.title = item.title;
      this.item.author = item.author;
      this.item.isbn = item.isbn;
      this.item.publisher = item.publisher;
      this.item.image = item.image;
      this.$emit("update:keyword",'');
      //this.$emit("update:items",[]);
      this.$emit("update:index",1);
      this.$emit("update:visible", false);
    },
    ChangeIndex(index){
      console.log("page:"+index);
      this.$emit("update:index",index);
      //this.$emit("update:items",[]);
    }
  },
  watch: {
    checks: function(v) {
      this.checkForm();
    }
  }
};
</script>
